import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-mandate-summary',
  templateUrl: './mandate-summary.component.html',
  styleUrls: ['./mandate-summary.component.scss']
})
export class MandateSummaryComponent implements OnInit {
  // accountId =  1010; 
  accountIdMandate: any;
  title = "Mandate Details";
  allMandate: any[];
  accountInfo: any;

  constructor(private router: Router, private apiService: ApiService, private localstore: LocalStoreService,
    public dialogRef: MatDialogRef<MandateSummaryComponent>, private dataService: DataService) { }

    ngOnInit(): void {
      this.accountIdMandate = this.localstore.getItem('accountId');
      this.accountInfo = this.localstore.getItem('EDIT-CREATEACCCIF');
      if(this.accountInfo){
        console.log("AccountInfo")
        this.accountIdMandate=this.accountInfo.businessProductAccount.accountId
      }
     
      console.log("Account ID from Local Storage", this.accountIdMandate);
      this.gettingallmandateDetail(this.accountIdMandate);
    
      console.log(this.accountInfo);
     
    }
    goToClose(){
      this.dialogRef.close(); 
    }
   

    gettingallmandateDetail(accountId) {
      this.apiService.gettingMandateDetails(this.accountIdMandate).subscribe((allTransactionResp : any) => {
      console.log("All Transaction Details",allTransactionResp);
      this.allMandate = allTransactionResp;

    });
  }
  goToEdit() {
    this.dialogRef.close();
    let payload = this.allMandate
    // [
    //   {
    //     "mandateId": 2128,
    //     "productAccountId": 12345,
    //     "modeOfOperation": "Net Banking",
    //     "amountFrom": 1222.0,
    //     "amountTo": 212122.0,
    //     "totalSignaturesRequired": 2,
    //     "remarks": "Testing"
    //   }
    // ];

    this.dataService.sendNavParam(payload);
    this.router.navigate([`/home/application/mandate/${this.accountIdMandate}`]);
  }

}
