import { Component, Input, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-customer-info-view',
  templateUrl: './customer-info-view.component.html',
  styleUrls: ['./customer-info-view.component.scss']
})
export class CustomerInfoViewComponent implements OnInit {

  loanaccountID : any;
  customerInfoDetails : any;
  ownership : any;
  noOfApplicants : any;
  
  @Input() public customerInfo: any; 
  appEntrySummaryView: boolean = false;
  isApprovedSreen: any;
  loanInfo: any;

  constructor(
    private ls: LocalStoreService,
    private loader: AppLoaderService,
    private appApiService: ApplicationEntryStageService,
    public dialogRef: MatDialogRef<CustomerInfoViewComponent>,
    private router: Router,private api:ApiService,
  ) { }

  ngOnInit(): void {
    this.api.getApprovedStatus().subscribe((resp)=>{
      console.log("message",resp);
      this.isApprovedSreen=resp;
 
    })
    console.log('customerInfo :: ',this.customerInfo);
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    this.loadData(this.customerInfo);
  }

  loadData(customerInfo){
    if (customerInfo == null) {
      this.appEntrySummaryView = false;
      setTimeout(() => {
        this.getCustomerInfoDetails();
      }, 10);
      
    } else {
      if (customerInfo.isAppEntryView) {
        this.appEntrySummaryView = true;
        this.customerInfoDetails = customerInfo.data;
        this.ownership = this.customerInfoDetails[0].ownership;
        this.noOfApplicants = this.customerInfoDetails[0].numOfApplicants;
      }
    }
  }
      
  getCustomerInfoDetails() {
    this.loader.open();
    this.appApiService.fetchCustomersByLoanAccId(this.loanaccountID,'loan').subscribe((customerInfoDetails) => {
      if(customerInfoDetails.data){
        this.loader.close();
        this.customerInfoDetails = customerInfoDetails.data;
        this.ownership = this.customerInfoDetails[0].ownership;
        this.noOfApplicants = this.customerInfoDetails[0].numOfApplicants;
      }
    }, (err: any) => {
      this.loader.close();
    });
  }


  
  goToClose(): void {
    this.dialogRef.close();
  }

  goToEdit() {
    this.dialogRef.close();
     this.router.navigate([`/loan/account/customer-information`]);
  }
  goTo360Dashboard(){
    console.log("calling")
    this.router.navigateByUrl('/others/mainNavigation');
  }

}
