import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-nominees-summary',
  templateUrl: './nominees-summary.component.html',
  styleUrls: ['./nominees-summary.component.scss']
})
export class NomineesSummaryComponent implements OnInit {
  // productAccountId = 1556;
  productAccountId: any;
  title = "Nominee Details"
  street: any;
  zipCode: any;
  allNominee: any[];
  gaurdianDetail: any[];
  gaurdianDetails = true;
  accountInfo: any;

  constructor(private router: Router, private apiService: ApiService, private localstore: LocalStoreService,
    public dialogRef: MatDialogRef<NomineesSummaryComponent>, private dataService: DataService) { }

  ngOnInit(): void {
    this.productAccountId = this.localstore.getItem('accountId');
    this.accountInfo = this.localstore.getItem('EDIT-CREATEACCCIF');

    if(this.accountInfo){
    console.log(this.accountInfo)
      this.productAccountId=this.accountInfo.businessProductAccount.accountId
    }
    console.log("Account ID from Local Storage", this.productAccountId);
    this.gettingallnomineeDetail(this.productAccountId);
  }
  goToClose() {
    this.dialogRef.close();
  }


  gettingallnomineeDetail(accountId) {
    this.apiService.gettingNomineeDetails(this.productAccountId).subscribe((allTransactionResp: any) => {
      console.log("All Transaction Details", allTransactionResp, allTransactionResp.nominees[0].isNomineeMinor);
      this.allNominee = allTransactionResp.nominees;

    });
  }
  
  goToEdit() {
    this.dialogRef.close();
    let payload = this.allNominee;
    this.dataService.sendNavParam(payload);
    this.router.navigate([`/home/application/nominee/add/${this.productAccountId}`]);
  }

}
