import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-assessment-details-summary',
  templateUrl: './assessment-details-summary.component.html',
  styleUrls: ['./assessment-details-summary.component.scss']
})
export class AssessmentDetailsSummaryComponent implements OnInit {

 
  showEditOption: boolean = false;
  accountDetails: any;
  loanInfo: any;
  loanaccountID: any;
  constructor(public dialogRef: MatDialogRef<AssessmentDetailsSummaryComponent>, private localstore: LocalStoreService,
    private router: Router,
    private dataService: DataService,
    private apiService: ApiService,) { }

    ngOnInit(): void {
      this.loanInfo = this.localstore.getItem('LOAN_ACC_ARRAY');
      console.log(this.loanInfo);
      if(this.loanInfo){
        this.loanaccountID = this.loanInfo.loanAccountId;
      }
      setTimeout(() => {
        this.getAccountDetails();
      }, 10);
     
    }
  goToClose(): void {
    this.dialogRef.close();
  }
  goToEdit() {
    this.dialogRef.close();
   // this.dataService.sendNavParam(this.assetValued);
    this.router.navigate([`/loan/account/offerstage/offer-assessment`]);
  } 
  getAccountDetails(){
    this.apiService.getAssessment(this.loanaccountID?this.loanaccountID : 566).subscribe((accountDetails : any) => {
      console.log("All Account Service",accountDetails);
      this.accountDetails = accountDetails;
      
      
      });
  }

}
