import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { DocumentOcrService } from 'app/views/others/genric-document-upload/document-ocr.service';
import { KycBasicInfoUpdateComponent } from '../kyc-basic-info-update/kyc-basic-info-update.component';

@Component({
  selector: 'app-kyc-document-update',
  templateUrl: './kyc-document-update.component.html',
  styleUrls: ['./kyc-document-update.component.scss']
})
export class KycDocumentUpdateComponent implements OnInit {
  customerId: any;
  screenInfo={
    screenName:'KYC verification',
    data:'',
    isEdit:true
  }
  disableNextAtIndex1: any;

  constructor(
    private dialogRef: MatDialogRef<KycDocumentUpdateComponent>,
    private doOcrService:DocumentOcrService,
    private ls: LocalStoreService
   
  ) { }

  ngOnInit(): void {
    this.customerId = this.ls.getItem('customerId')
    this.ls.setItem('customer_id',this.customerId)
    this.screenInfo={
      screenName:'onboarding',
      data:'',
      isEdit:true
    }
    this.doOcrService.getenableNext()
    .subscribe(data=>{
      console.log("data disableNextAtIndex1 :: ",data);
      this.disableNextAtIndex1=data;
    });
  }
  onSubmit(){
    this.dialogRef.close({
      status:'INITIATED',
      screen:'documentUpload'
    })
  }
  close() {
    this.dialogRef.close({
      status: "UNDER_PROCESS"
    })
  }
}
