import { Component, OnInit } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import {WebcamImage, WebcamInitError, WebcamUtil  } from "ngx-webcam";
import { MatSnackBar } from '@angular/material/snack-bar';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { Router } from '@angular/router';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
@Component({
  selector: 'app-new-scan',
  templateUrl: './new-scan.component.html',
  styleUrls: ['./new-scan.component.scss']
})


export class NewScanComponent implements OnInit {
  [x: string]: any;
  private trigger: Subject<any> = new Subject();
  public webcamImage!: WebcamImage;
  private nextWebcam: Subject<any> = new Subject();
  sysImage = '';  
  error?: string;
  isDone:boolean=false;
  constructor(private snack: MatSnackBar,public dialogRef: MatDialogRef<NewScanComponent>, private ls: LocalStoreService, private router: Router) { }
  
  ngOnInit(): void {   
  }  
  retakebtn(){
    
    // this.captureImg;
    this.webcamImage = null;
    this.sysImage = null;    
    this.isDone=false;
    
  }
  public getSnapshot(): void {
    this.trigger.next();
  }
  public captureImg(webcamImage: WebcamImage): void {
    this.webcamImage = webcamImage;
    this.sysImage = webcamImage!.imageAsDataUrl;
    console.info('got webcam image', this.sysImage);
    this.isDone=true;
  }
  public get invokeObservable(): Observable<any> {
    return this.trigger.asObservable();    
  }
  public get nextWebcamObservable(): Observable<any> {
    return this.nextWebcam.asObservable();
  }
  public handleInitError(error: WebcamInitError): void {
    this.errors.push(error);
  }
  onSubmit(){
    console.log(this.sysImage);
    this.snack.open('File Uploded', 'OK', {
      duration: 4000,
      verticalPosition: 'top',
      horizontalPosition: 'right'
    });
    this.ls.setItem("imageForScan",this.sysImage);   
    this.dialogRef.close({
      resImage:this.sysImage
    });
  }

  closeClick(){
     this.dialogRef.close('close')
  }

  

}
