import { Component, Input, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-card-account-summary',
  templateUrl: './card-account-summary.component.html',
  styleUrls: ['./card-account-summary.component.scss']
})
export class CardAccountSummaryComponent implements OnInit {
  @Input() public accountServiceInfo:any;
  appEntrySummaryView : boolean = false;
  productAccountId: any;
  accountStatement: any;
  bankPreference: any = [];
  statementPreference: any = [];
  statementPreferenceFetched: any[];
  checkBook: any;
  bankChannel: any;
  commChannel: any = [];
  commChannelFetched: any[];
  passBook: any;
  summaryResponse: any;
  cardId:any;
  allAccountServiceResp: any;
  isApprovedSreen: any;
  constructor(private router: Router, private apiService: ApiService, private localstore: LocalStoreService,
    public dialogRef: MatDialogRef<CardAccountSummaryComponent>, private dataService: DataService) { }

  ngOnInit(): void {
    this.cardId = this.localstore.getItem('cardId');
    console.log("Account ID from Local Storage", this.productAccountId);
    this.apiService.getAppViewSummary().subscribe((resp)=>{
      console.log("message",resp);
      this.isApprovedSreen=resp;
     
 
    })
    this.gettingallAccountService();

    this.loadData(this.accountServiceInfo)
  }
  goToClose() {
    this.dialogRef.close();
  }
  // loadData(accountServiceInfo){
  //   if (accountServiceInfo == null) {
  //     this.appEntrySummaryView = false;
  //     this.gettingallAccountService();
  //   } else {
  //     if (accountServiceInfo.isAppEntryView) {
  //       this.appEntrySummaryView = true;
  //       this.allAccountServiceResp = accountServiceInfo.data;
       
  //       this.accountStatement = this.allAccountServiceResp.statementFrequency;
  //       this.statementPreferenceFetched = this.allAccountServiceResp.statementPref;
  //     }
  //   }
  // }
  loadData(accountServiceInfo){
    if (accountServiceInfo == null) {
      this.appEntrySummaryView = false;
      this.gettingallAccountService();
    } else {
      if (accountServiceInfo.isAppEntryView) {
        this.appEntrySummaryView = true;
        this.accountServiceInfo = accountServiceInfo.data;
        this.accountStatement = this.allAccountServiceResp.statementFrequency;
        this.statementPreferenceFetched = this.allAccountServiceResp.statementPref;
        this.commChannelFetched = this.allAccountServiceResp.commChannelPref;
        this.summaryResponse = this.allAccountServiceResp;
      }
    }
  }
      
  // getCustomerInfoDetails() {
  //   this.loader.open();
  //   this.appApiService.fetchCustomersByLoanAccId(this.loanaccountID,'loan').subscribe((customerInfoDetails) => {
  //     if(customerInfoDetails.data){
  //       this.loader.close();
  //       this.customerInfoDetails = customerInfoDetails.data;
  //       this.ownership = this.customerInfoDetails[0].ownership;
  //       this.noOfApplicants = this.customerInfoDetails[0].numOfApplicants;
  //     }
  //   }, (err: any) => {
  //     this.loader.close();
  //   });
  // }
  gettingallAccountService() {
    this.apiService.gettingCardAccountServiceDetails(this.cardId).subscribe((allAccountServiceResp: any) => {
      console.log("All Account Service", allAccountServiceResp);
       this.accountServiceInfo = allAccountServiceResp.data;
      this.accountStatement = allAccountServiceResp.statementFrequency;
      this.statementPreferenceFetched = allAccountServiceResp.statementPref;
      for (let index = 0; index < this.statementPreferenceFetched.length; index++) {
        const element = this.statementPreferenceFetched[index];
        if(element.checked) {
          !this.statementPreference.includes(element.name) && this.statementPreference.push(element.name);
        }
      }
      this.commChannelFetched = allAccountServiceResp.commChannelPref;
      for (let index = 0; index < this.commChannelFetched.length; index++) {
        const element = this.commChannelFetched[index];
        if(element.checked) {
          !this.commChannel.includes(element.name) && this.commChannel.push(element.name);
        }
      }
      this.summaryResponse = allAccountServiceResp;
    });
  }
  goToEdit() {
    this.dialogRef.close();
    this.dataService.sendNavParam(this.summaryResponse);
    this.router.navigate([`/cardaccount/applicationEntryStage/list/accountServices`]);
  }
}
