import { Injectable } from '@angular/core';
import { AppConstants } from 'app/config/app.constant';
import { Subject, Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import * as moment from 'moment';
export const API_URL = AppConstants.baseURL;
@Injectable({
  providedIn: 'root'
})
export class VideoService {

  private subject = new Subject<any>();
  constructor(private http:HttpClient) { }

  sendUserDetailsOFVideo(message: any) {
    
    this.subject.next(message);
}
getUserDetailsOfVideo(): Observable<any> {
  return this.subject.asObservable();
}
saveCustBasicInfoData(icustCustVideoVerifyDto)
{
 return this.http.post<any>(`${API_URL}/icust-video-verify/save`,icustCustVideoVerifyDto)
 
}
getAppointmentLinkDetails(firstName,lastName,phoneNumber)
{
  return this.http.get(`${API_URL}/icust-video-verify/appointment/${firstName}/${lastName}/${phoneNumber}`)
}
convertDate(value){
  let date = moment(value).format('YYYY-MM-DD');
   console.log("date", date);
  if(date)
  return date;
}

}
