import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-account-create-summary',
  templateUrl: './account-create-summary.component.html',
  styleUrls: ['./account-create-summary.component.scss']
})
export class AccountCreateSummaryComponent implements OnInit {
  accountCreateValue: any;
  loanInfo: any;
  loanaccountID: any;

  constructor(public dialogRef: MatDialogRef<AccountCreateSummaryComponent>, private router: Router,private ls:LocalStoreService,
    private api: ApiService) { }

  ngOnInit(): void {
    this.loanaccountID = localStorage.getItem('loanAccountId')
    this.loanInfo =this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.getAccountCreateDetails();
    }, 10);
   
  }
 
  goToClose(): void {
    this.dialogRef.close();
  }
  goToEdit() {
    this.dialogRef.close();
    this.router.navigate([`/loan/account/account-creation-stage/account-create-details`]);
    

    // this.router.navigate([`/home/application/customer-information/${this.productAccountIdCust}`]);
  }
  getAccountCreateDetails() {
    this.api.getAccountCreateDetails(this.loanaccountID)
     .subscribe((accountCreateDetails: any)=> {
      console.log(accountCreateDetails);
      this.accountCreateValue=accountCreateDetails;
      
    });
  }
}
