import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-valuation-assets-summary',
  templateUrl: './valuation-assets-summary.component.html',
  styleUrls: ['./valuation-assets-summary.component.scss']
})
export class ValuationAssetsSummaryComponent implements OnInit {
  ValuationDetailsValues: any;
  loanaccountID: any;
  loanInfo: any;

  constructor(public dialogRef: MatDialogRef<ValuationAssetsSummaryComponent>,
     private router: Router,
     private api: ApiService,    
     private ls: LocalStoreService,
    ) { }

  ngOnInit(): void {
    this.loanaccountID = this.ls.getItem('loanAccountId');
    console.log("loanAccountId::", this.loanaccountID)
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.gettingValuationDetail();
    }, 10);
   
  }
  goToClose(): void {
    this.dialogRef.close();
  }
  goToEdit() {
    this.dialogRef.close();
    this.router.navigate([`/loan/account/list/underwritingStage/valuationofassets`]);

    // this.router.navigate([`/home/application/customer-information/${this.productAccountIdCust}`]);
  }
   
  gettingValuationDetail() {
    this.api.gettingValuationDetails(this.loanaccountID)
      .subscribe((ValuationDetails: any) => {
       console.log(ValuationDetails);
       this.ValuationDetailsValues=ValuationDetails;
      //  this.buildCollateralForm(resp,true);
      });
  }

}
