import { Component, Input, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-card-prefrence-summary',
  templateUrl: './card-prefrence-summary.component.html',
  styleUrls: ['./card-prefrence-summary.component.scss']
})
export class CardPrefrenceSummaryComponent implements OnInit {
  cardPrefrenceDetails:any;
  cardId: void;
  @Input() public cardPreferenceInfo:any;
  appEntrySummaryView: boolean = false;

  constructor( public dialogRef: MatDialogRef<CardPrefrenceSummaryComponent>,private loader: AppLoaderService,
    private apiService: ApiService,private ls: LocalStoreService, private router:Router) { }

  ngOnInit(): void {
    this.cardId=this.ls.getItem('cardId');
    this.loader.open();
    this.getCardPrefrenceDetails();
    this.loadData(this.cardPreferenceInfo);
    this.loader.close();
  }
  goToClose(){
    this.dialogRef.close();
  }
  getCardPrefrenceDetails() {
    this.apiService.getCardPrefrence(this.cardId).subscribe((cardDetails) => {
      console.log(cardDetails)
      if(cardDetails){
        this.cardPrefrenceDetails = cardDetails;
      }
    }, (err: any) => {
    });
  }
  loadData(cardPreferenceInfo){
    if (cardPreferenceInfo == null) {
      this.appEntrySummaryView = false;
      this.getCardPrefrenceDetails();
    } else {
      if (cardPreferenceInfo.isAppEntryView) {
        this.appEntrySummaryView = true;
        this.cardPrefrenceDetails = cardPreferenceInfo.data;
      }
    }
  }


  goToEdit(){
    this.dialogRef.close();
     this.router.navigate([`/cardaccount/applicationEntryStage/list/cardPrefrence`]);

  }
}
