import { Component, Inject, OnInit, ViewEncapsulation } from '@angular/core';
import {
  ControlValueAccessor,
  NG_VALUE_ACCESSOR,
  FormBuilder,
  FormGroup,
  Validators,
  FormControl,
  NG_VALIDATORS,
} from "@angular/forms";
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { DataService, DOCUMENT_PROCESS_TYPE } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { CutomerFingerPrintComponent } from '../../cutomer-finger-print/cutomer-finger-print.component';
import { KycFaceScanComponent } from '../../kyc-face-scan/kyc-face-scan.component';
import { KycIrisScanComponent } from '../../kyc-iris-scan/kyc-iris-scan.component';
import { VerificationCompletedComponent } from '../../verification-completed/verification-completed.component';



@Component({
  selector: 'app-kyc-screening-newcustomer',
  templateUrl: './kyc-screening-newcustomer.component.html',
  styleUrls: ['./kyc-screening-newcustomer.component.scss'],
  encapsulation: ViewEncapsulation.Emulated
})
export class KycScreeningNewcustomerComponent implements OnInit {
  newCustomerForm: FormGroup;
  optionType: any;
  isCustomer: any;
  //isExistingCustomer:boolean=true;
  isAuto: boolean = true;
  isScan: boolean = true;
  radioFlag: any = 'newCustomer';
  isClick: boolean = true;
  isOtp: boolean = false;
  isBiometric: boolean = false;
  isExistingCustomer: boolean = false;
  isEdit: boolean = false;
  rekyc: boolean = false;
  documentName: any;
  documentNumber: any;
  docType: string[];
  customerId: any;
  imageURLBioScan: string = 'not_available';
  imageURLFace: any = 'not_available';
  imageURLIrish: any = 'not_available';
  otpValue: any
  docUploaded: boolean = false;
  aadharData: any;
  resendCount: number = 0;
  timer: any;


  constructor(private dialogRef: MatDialogRef<KycScreeningNewcustomerComponent>,
    private apiService: ApiService, private dialog: MatDialog,
    private snack: MatSnackBar,
    private dataService: DataService,
    @Inject(MAT_DIALOG_DATA) private data: any
  ) { }

  ngOnInit(): void {
    console.log(this.data);

    if (this.data.rekyc == true) {
      this.isExistingCustomer = true;
      this.rekyc = true
      this.customerId = this.data.customerId
    }
    if (this.data.auto == false) {
      this.isAuto = false
    }
    else {
      this.isAuto = true;
    }
    this.getDocumentType();
  }
  customerToggle(event) {
    if (event == true) {
      this.isExistingCustomer = true;
      this.isAuto = true;
      this.isOtp = false;
      this.isBiometric = false;
      this.isScan = true;
      // this.documentName = '';
      // this.documentNumber = '';
      // this.otpValue = '';
      this.customerId = '';
      this.clearAllData();
    }
    else {
      this.isExistingCustomer = false;
      this.isAuto = true;
      this.isOtp = false;
      this.isBiometric = false;
      // this.documentName = '';
      // this.documentNumber = '';
      // this.otpValue = '';
      // this.imageURLBioScan = 'not_available';
      // this.imageURLFace = 'not_available';
      // this.imageURLIrish  = 'not_available';
      // this.docUploaded = false;
      // this.aadharData = {};
      // this.otpValue = ''
      this.clearAllData();
    }
  }
  radioEvent(event) {
    if (event == 'newCustomer') {
      this.isClick = true;
      this.radioFlag = event;
    }
    else {
      this.isClick = false;
      this.radioFlag = event;
    }
    // console.log("radio button event");
    //this.radioFlag = event.target.value;
  }
  verificationMethodToggle(event) {
    if (!this.documentName || !this.documentNumber) {
      return;
    }
    if (event == 'otp') {
      this.isScan = true;
      this.isOtp = true;
      this.isBiometric = false;
      // this.imageURLBioScan = 'not_available';
      // this.imageURLFace = 'not_available';
      // this.imageURLIrish  = 'not_available';
      this.clearAutoData();
    }
    else {
      this.isScan = false;
      this.isBiometric = true;
      this.isOtp = false;
      // this.resendCount = 0;
      // this.otpValue = '';
      this.clearAutoData();
    }
    // console.log("radio button event");
    //this.radioFlag = event.target.value;
  }

  onAutoToggle(type: string) {
    if (type == 'auto') {
      this.isAuto = true;
      this.dataService.setDocumentProcessType(type);
      console.log(this.isAuto);
      // this.documentName = '';
      // this.documentNumber = '';
      this.docUploaded = false;
      this.isOtp = false;
      this.isBiometric = false;
      // this.docUploaded = false;
      // this.aadharData = {};
      this.clearAllData();
    } else {
      this.isAuto = false;
      this.dataService.setDocumentProcessType(type);
      console.log(this.isAuto);
      // this.imageURLBioScan = 'not_available';
      // this.imageURLFace = 'not_available';
      // this.imageURLIrish  = 'not_available';
      // this.otpValue = '';
      // this.resendCount = 0;
      this.clearAllData();

    }
  }



  _keyPress(event) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  getDocumentType() {
    this.apiService.getDropDownValues('document_type').subscribe(res => this.docType = res)
  }
  onSkip() {
    this.dialogRef.close('skip');
  }

  reKycToggle(event) {
    if (!this.customerId) {
      return;
    }
    if (event == true) {
      this.rekyc = true;
      this.isEdit = false;
      this.isAuto = true;
      this.isOtp = false;
      this.isBiometric = false

    }
    else {
      this.rekyc = false;
      this.isEdit = true;
      this.imageURLBioScan = 'not_available';
      this.imageURLFace = 'not_available';
      this.imageURLIrish = 'not_available';
      this.docUploaded = false;

    }
  }
  getCustomerDetails() {
    this.apiService.gettingCustomerDetails(this.customerId).subscribe(res => {
      if (!res) {
        this.snack.open('Please Enter Valid CIF Number', 'Ok!', {
          duration: 3000,
          horizontalPosition: 'right',
          verticalPosition: 'top'
        })
        this.customerId = '';
      }
    }, err => {
      this.snack.open('Please Enter Valid CIF Number', 'Ok!', {
        duration: 3000,
        horizontalPosition: 'right',
        verticalPosition: 'top'
      })
      this.customerId = '';
    })
  }
  openFingerprintPopup() {
    const dialogRef = this.dialog.open(CutomerFingerPrintComponent, {
      data: { verificationType: 'sigin' },
      disableClose: true,
      width: '40%'
    });
    dialogRef.afterClosed().subscribe((submit) => {
      if (submit.isScanned) {
        this.imageURLBioScan = 'data:image/bmp;base64,' + submit.BMPBase64
      }
      else {
        this.imageURLBioScan = 'failed'
      }
    });
  }
  openIrisPopup() {
    const dialogRef = this.dialog.open(KycIrisScanComponent, {
      disableClose: true,
      width: '40%'
    });
    dialogRef.afterClosed().subscribe((submit) => {
      console.log('submit :: ', submit);
      if (submit.isScanned) {
        this.imageURLIrish = submit.fileUrl
      }
    });
  }
  openFacePopup() {
    const dialogRef = this.dialog.open(KycFaceScanComponent, {
      disableClose: true,
      width: '40%'
    });
    dialogRef.afterClosed().subscribe((submit) => {
      console.log('submit :: ', submit);
      if (submit.isScanned) {
        this.imageURLFace = submit.image;
      }
    });
  }
  onSubmit() {
    console.log(this.documentName);
    console.log(this.documentNumber);
    this.dialogRef.close({
      customerId: this.customerId,
      rekyc: this.rekyc,
      isEdit: this.isEdit,
      dataForForm: this.aadharData
    })

  }
  onUpload(event) {
    console.log(event);
    this.docUploaded = event;
  }
  getData(event) {
    console.log(event);
    this.aadharData = event;

  }
  onClickResend() {
    if (this.resendCount == 3) {
      this.snack.open('Maximum Resend Attempt Reached', 'Ok', {
        duration: 3000,
        horizontalPosition: 'right',
        verticalPosition: 'top'
      })
      return;
    }
    this.resendCount++;
    this.snack.open('Otp Sent Again Successfully', 'OK', {
      duration: 1000,
      horizontalPosition: 'right',
      verticalPosition: 'top'
    })
  }
  _keypress1(event) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  onKeyUp1() {
    clearTimeout(this.timer)
    this.timer = setTimeout(() => {
      this.isOtp = false;
      this.isBiometric = false;
      this.clearAutoData();
    }, 1000);
  }
  onKeyUp() {
    clearTimeout(this.timer)
    this.timer = setTimeout(() => {
      this.clearAllData();
      this.rekyc = false;
      this.isEdit = false;
    }, 1000);
  }
  clearAutoData() {
    this.resendCount = 0;
    this.otpValue = '';
    this.imageURLBioScan = 'not_available';
    this.imageURLIrish = 'not_available';
    this.imageURLFace = 'not_available';
  }
  clearAllData() {
    this.documentName = '';
    this.documentNumber = '';
    this.resendCount = 0;
    this.otpValue = '';
    this.imageURLBioScan = 'not_available';
    this.imageURLIrish = 'not_available';
    this.imageURLFace = 'not_available';
    this.docUploaded = false;
    this.aadharData = {};
  }

}

