import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ApiService } from 'app/shared/services/api.service';
import { VehicleDetailsComponent } from 'app/views/loan-account/loan-entry-stage/vehicle-details/vehicle-details.component';
@Component({
  selector: 'app-video-verification-popup',
  templateUrl: './video-verification-popup.component.html',
  styleUrls: ['./video-verification-popup.component.scss']
})
export class VideoVerificationPopupComponent implements OnInit {
  docType: string[];
  aadharData: any;
  docUploaded: boolean = false;

  constructor(public dialogRef: MatDialogRef<VideoVerificationPopupComponent>,
    private apiService: ApiService
    ) { }

  ngOnInit(): void {
    this.getDocumentType();
  }
  getDocumentType(){
    this.apiService.getDropDownValues('document_type').subscribe(res => this.docType = res)
  }
  onUpload(event){
    console.log(event);
    this.docUploaded = event;
  }
  getData(event){
    console.log(event);
    this.aadharData = event;
  }
  onSubmit(){    
    this.dialogRef.close({
      dataForForm: this.aadharData
    })

  }
  onSkip(){
    this.dialogRef.close('skip');
  }
}
