import { Component, Input, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-card-customer-info-summary',
  templateUrl: './card-customer-info-summary.component.html',
  styleUrls: ['./card-customer-info-summary.component.scss']
})
export class CardCustomerInfoSummaryComponent implements OnInit {
@Input() public cardCustomerInfo:any;
  customerInfoDetails : any;
  ownership : any;
  noOfApplicants : any;
  
 
  appEntrySummaryView: boolean = false;
  isApprovedSreen: any;
  customerInfo: any;
  cardId: any;

  constructor(
    private ls: LocalStoreService,
    private loader: AppLoaderService,
    private appApiService: ApplicationEntryStageService,
    public dialogRef: MatDialogRef<CardCustomerInfoSummaryComponent>,
    private router: Router,private api:ApiService,
  ) { }

  ngOnInit(): void {
    
    this.api.getAppViewSummary().subscribe((resp)=>{
      console.log("message",resp);
      this.isApprovedSreen=resp;
     
 
    })
    this.cardId = this.ls.getItem('cardId');
    console.log(" this.cardId", this.cardId);
    this.getCustomerInfoDetails();
  
   this.loadData(this.cardCustomerInfo);
    
  }   
  loadData(cardCustomerInfo){
    if (cardCustomerInfo == null) {
      this.appEntrySummaryView = false;
      this.getCustomerInfoDetails();
    } else {
      if (cardCustomerInfo.isAppEntryView) {
        this.appEntrySummaryView = true;
        this.customerInfoDetails = cardCustomerInfo.data;
        console.log(this.customerInfoDetails);
        
        this.ownership = this.customerInfoDetails[0].ownership;
        this.noOfApplicants = this.customerInfoDetails[0].numOfApplicants;
        this.ownership = this.ls.getItem("cardownership_value");
        this.noOfApplicants = this.ls.getItem("CardNoOfApplicants");
      }
    }
  }
  getCustomerInfoDetails() {
   // this.loader.open();
    this.appApiService.fetchCardCustomersByCardId( this.cardId,'cardOrigin').subscribe((customerInfoDetails) => {
      if(customerInfoDetails.data){
     //   this.loader.close();
        this.customerInfoDetails = customerInfoDetails.data;
        this.ownership = this.customerInfoDetails[0].ownership;
        this.noOfApplicants = this.customerInfoDetails[0].numOfApplicants;
        this.ownership = this.ls.getItem("cardownership_value");
        this.noOfApplicants = this.ls.getItem("CardNoOfApplicants");
      }
    }, (err: any) => {
      this.loader.close();
    });
  }


  
  goToClose(): void {
    this.dialogRef.close();
  }

  goToEdit() {
    this.dialogRef.close();
     this.router.navigate([`/cardaccount/applicationEntryStage/list/customerInformation`]);
  }
  

}
