import { HttpClient, HttpErrorResponse, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AppConstants } from 'app/config/app.constant';
import { ShowMessageService } from 'app/shared/services/showMessage/show-message.service';
import { BehaviorSubject, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
export const API_URL = AppConstants.baseURL;
export const LOAN_URL = AppConstants.loanURL;

@Injectable({
  providedIn: 'root'
})
export class TasksummaryService {
 

  accountStatus: BehaviorSubject<any> = new BehaviorSubject<any>('');
  accountStatusData(data) {
    this.accountStatus.next(data);
  }
  cardAccountStatus: BehaviorSubject<any> = new BehaviorSubject<any>('');
  cardStatusData(data){
    this.cardAccountStatus.next(data);
  }
  loanAccountStatus: BehaviorSubject<any> = new BehaviorSubject<any>('');
  loanStatusData(data){
    this.loanAccountStatus.next(data);
  }

  constructor(private http: HttpClient, private showMessageService: ShowMessageService) { }

  private errorHandler = (errorResp: HttpErrorResponse) => {
    console.error('Error : ' + errorResp.message);
    console.error('status : ' + errorResp.status);
    this.showMessageService.errorMessage(errorResp.status, errorResp);
    return throwError(errorResp);
  }

  getAllAccDetails(status) {
    const statusVal = status;
    const params = new HttpParams().append('status', status);
    return this.http.get<any[]>(`${API_URL}/createAccTaskSummary/getAllAccInfo?${params}`).pipe(catchError(this.errorHandler));;
  }

  getAllAccDetailOnScroll(status, page) {
    // console.log('status:: ', status);
    // console.log('page:: ', page);
    const params = new HttpParams().append('status', status).append('page', page);
    // params.append('size', size);

    return this.http.get<any[]>(`${API_URL}/createAccTaskSummary/getAllAccInfo?${params}`).pipe(catchError(this.errorHandler));;
  }

  getSearchData(params) {
    return this.http.get<any>(`${API_URL}/createAccTaskSummary/getAllAccInfo?${params}`).pipe(catchError(this.errorHandler));;
  }

  getCardSeracData(params,status) {
    return this.http.get<any>(`${LOAN_URL}/cardOrigin/fetchTaskSummaryDetails?${params}&status=${status}`).pipe(catchError(this.errorHandler));;
  }
  
  // Card Origination Task Summary
  getCardAccDetails(status) {
    const statusVal = status;
    const params = new HttpParams().append('status', status);
    return this.http.get<any[]>(`${LOAN_URL}/cardOrigin/fetchTaskSummaryDetails?${params}`).pipe(catchError(this.errorHandler));
  }
  
  getAllCardAccDetailOnScroll(status, page) {
    const params = new HttpParams().append('status', status).append('page', page);
    return this.http.get<any[]>(`${LOAN_URL}/cardOrigin/fetchTaskSummaryDetails?${params}`).pipe(catchError(this.errorHandler));;
  }

  getCardAccSearchData(params) {
    return this.http.get<any>(`${LOAN_URL}/cardOrigin/fetchTaskSummaryInfo?${params}`).pipe(catchError(this.errorHandler));
  }

  getCardAccSearchData1(params) {
    return this.http.get<any>(`${LOAN_URL}/cardOrigin/fetchTaskSummaryDetails?cardId=${params}`).pipe(catchError(this.errorHandler));
  }

  getCardAccountInfoById(cardId){
    const statusVal = cardId;
    const params = new HttpParams().append('cardId', cardId);
    return this.http.get<any[]>(`${LOAN_URL}/cardOrigin/fetchTaskSummaryInfo?${params}`).pipe(catchError(this.errorHandler));;
  }

  getAccountInfoByAccId(accountId) {
    return this.http.get<any>(`${API_URL}/createAccTaskSummary/getAccInfo/${accountId}`).pipe(catchError(this.errorHandler));
  }

  updateAccountInfoByAccId(accountForm) {
    return this.http.put<any>(`${API_URL}/productAccount/updateStatus`, accountForm).pipe(catchError(this.errorHandler));
  }

  updateCreateAccStatus(accountForm){
    return this.http.post<any>(`${API_URL}/accountdata/updateAccountStatus`, accountForm).pipe(catchError(this.errorHandler));
  }

  updateCardAccountStatus(cardForm){
    return this.http.put<any>(`${LOAN_URL}/cardInitiation-api/updateStatus`, cardForm).pipe(catchError(this.errorHandler));
  }
  getAllCustOnBoardingData(status, page) {
    var params;
    if ((status != null || status != '' || status.length != 0) && page == 0) {
      console.log('only status')
      params = new HttpParams().append('custStatus', status);
    } else {
      // console.log('status and page')
      params = new HttpParams().append('custStatus', status).append('page', page);
    }

    return this.http.get<any>(`${API_URL}/customerdata/getCustomerInfo?${params}`).pipe(catchError(this.errorHandler));
  }

  getAccountInfo(paramKey,paramValue) {
    // var params;
    // if ((accountId != null || accountId != '' || accountId !=0)) {
    //   console.log('only accountId')
    //   params = new HttpParams().append('accountId', accountId);
    // }

    var params;
    const arr =['customerId','accountId'];
    arr.filter(item => {
      
      if(item.includes(paramKey)){
        console.log('item', item);
        params = new HttpParams().append(`${item}`, paramValue);
      }
    });

    return this.http.get<any>(`${API_URL}/createAccTaskSummary/getAllAccInfo?${params}`).pipe(catchError(this.errorHandler));
  }

  updateCustOnboradInfo(formData) {
    return this.http.put<any>(`${API_URL}/customerdata/updateAccountSetting`, formData).pipe(catchError(this.errorHandler));
  }
  getallCorporateData(screenType,status, page) {
    var params;
    if ((status != null || status != '' || status.length != 0) && page == 0) {
      console.log('only status')
      params = new HttpParams().append('screenType', screenType).append('status', status);
    } else {
      // console.log('status and page')
      params = new HttpParams().append('screenType', screenType).append('status', status).append('page', page);
    }
    return this.http.get<any>(`${API_URL}/onBoarding/fetchCustomerInfo?${params}`).pipe(catchError(this.errorHandler));
  }

  getallLoanAccountData(status) {
    var params;
    // if ((status != null || status != '' || status.length != 0) && page == 0) {
    //   console.log('only status')
      params = new HttpParams().append('status', status);
    // } else {
    //   // console.log('status and page')
    //   params = new HttpParams().append('screenType', screenType).append('status', status).append('page', page);
    // }
    return this.http.get<any>(`${LOAN_URL}/loanAccount/fetchTaskSummaryDetails?${params}`).pipe(catchError(this.errorHandler));
  }

  getLoanAccountSearchSummary(params) {
    
    return this.http.get<any>(`${LOAN_URL}/loanAccount/fetchTaskSummaryDetails?${params}`).pipe(catchError(this.errorHandler));
  }

  getallLoanAccountData1(params,status) {
    return this.http.get<any>(`${LOAN_URL}/loanAccount/fetchTaskSummaryDetails?${params}&status=${status}`).pipe(catchError(this.errorHandler));;
  }

  getallLoanAccountData2(text) {
    return this.http.get<any>(`${LOAN_URL}/loanAccount/fetchTaskSummaryDetails?loanAccountId=${text}`).pipe(catchError(this.errorHandler));;
  }

  getAllLoanDetailOnScroll(status, page) {
    // console.log('status:: ', status);
    // console.log('page:: ', page);
    const params = new HttpParams().append('status', status).append('page', page);
    // params.append('size', size);

    return this.http.get<any>(`${LOAN_URL}/loanAccount/fetchTaskSummaryDetails?${params}`).pipe(catchError(this.errorHandler));;
  }

  getLoanAccountDetails(loanAccountId){
    var params;
    params = new HttpParams().append('loanAccountId', loanAccountId);

    return this.http.get<any[]>(`${LOAN_URL}/loanAccount/fetchTaskSummaryInfo?${params}`).pipe(catchError(this.errorHandler));;

  }
  updateLoanAccStatus(accountForm){
    return this.http.put<any>(`${LOAN_URL}/application-entrystage-api/updateStatus`, accountForm).pipe(catchError(this.errorHandler));
  }

  getCardSearchData(params) {
    return this.http.get<any>(`${LOAN_URL}/cardOrigin/fetchTaskSummaryDetails?${params}`).pipe(catchError(this.errorHandler));;
  }
 
  
}
