import { ChangeDetectorRef, Injectable } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { FileUploadService } from 'app/shared/services/file-upload.service';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class DocumentOcrService {
  nameDoc: any[] = [];
  addressArray: any[] = [];
  pincode: any[] = [];
  gender: any[] = [];
  state: any[] = [];
  dateOfIssue: any[] = [];
  dateOfExpiry: any[] = [];

  constructor(
    public dialog: MatDialog,
    private snack: MatSnackBar,
    private dialogService: DialogService,
    private loader: AppLoaderService,
    private fileService: FileUploadService
  ) { }

  doOCR(img, docVal, selectedFile, docSideEnum, i, documentValFromUI, nameFromForm): Observable<any> {
    let subject = new Subject();
    this.loader.open();
    console.log("in do ocr ", documentValFromUI, docSideEnum, i);

    // :: Verify Pan card ::
    if (documentValFromUI === 'Pan card') {
      this.fileService.uploadPan(selectedFile).subscribe(
        (res) => {
          this.loader.close();
          if (typeof res === 'object') {
            console.log('api res -- ', res.data);
            if (!res.data['name'].value || !res.data['pan'].value) {
              this.snack.open(
                'The document is not a valid Pan card',
                'Cancel',
                {
                  duration: 4000,
                  verticalPosition: 'top',
                  horizontalPosition: 'center',
                }
              );
              return subject;
            }
            // this.nameDoc = res.data['name'].value;
            this.dialogService
              .confirmationDialog(res.data['name'].value, nameFromForm)
              .subscribe((response) => {
                console.log(response);
                if (response == 'No') {
                  // this.changeValue2_Ocr(docVal, i);
                  var obj = {
                    response: 'No'
                  }
                  subject.next(obj);
                  return subject;
                }
                if (response == 'Yes') {
                  var successObject = {
                    response: 'Yes',
                    data: {
                      nameOnCard: res.data['name'].value,
                      dobArray: res.data['dob'].value,
                      idNumberArray: res.data['pan'].value,
                      dateOfIssue: this.dateOfIssue.length > 0 ? this.dateOfIssue[i] : [],
                      dateOfExpiry: this.dateOfExpiry.length > 0 ? this.dateOfExpiry[i] : [],
                      address: this.addressArray.length > 0 ? this.addressArray[i] : [],
                      pincode: this.pincode.length > 0 ? this.pincode[i] : [],
                      gender: this.gender.length > 0 ? this.gender[i] : [],
                      state: this.state.length > 0 ? this.state[i] : []
                    },
                    documentName: documentValFromUI
                  }
                  subject.next(successObject);
                  return subject;
                }
              });
          }
        },
        (err) => {
          console.log('Something went wrong! Err:: ', err);
          this.loader.close();
          this.snack.open('The document is not a valid Pan card', 'Cancel', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'center',
          });
          return subject;
        }
      );
    }
    // :: Verify Passport ::
    if (documentValFromUI == 'Passport') {
      this.fileService.uploadPassport(selectedFile).subscribe(
        (res) => {
          this.loader.close();
          if (typeof res === 'object') {
            console.log('api res -- ', res.data);
            if (!res.data['passport_no'].value) {
              this.snack.open(
                'The document is not a valid Passport',
                'Cancel',
                {
                  duration: 4000,
                  verticalPosition: 'top',
                  horizontalPosition: 'center',
                }
              );
              return subject;
            }
            this.nameDoc = !!res.data?.['surname']?.value
              ? `${res.data['given_name'].value} ${res.data?.['surname']?.value}`
              : res.data['given_name'].value;
            this.dialogService
              .confirmationDialog(this.nameDoc, nameFromForm)
              .subscribe((response) => {
                console.log(response);
                if (response == 'No') {
                  // this.changeValue2_Ocr(docVal, i);
                  var obj = {
                    response: 'No'
                  }
                  subject.next(obj);
                  return subject;

                }
                if (response == 'Yes') {
                  var successObject = {
                    response: 'Yes',
                    data: {
                      nameOnCard: !!res.data?.['surname']?.value
                        ? `${res.data['given_name'].value} ${res.data?.['surname']?.value}`
                        : res.data['given_name'].value,
                      dobArray: res.data['dob'].value,
                      idNumberArray: res.data['passport_no'].value,
                      dateOfIssue: res.data['issue_date'].value,
                      dateOfExpiry: res.data['expiry_date'].value,
                      address: this.addressArray.length > 0 ? this.addressArray[i] : [],
                      pincode: this.pincode.length > 0 ? this.pincode[i] : [],
                      gender: this.gender.length > 0 ? this.gender[i] : [],
                      state: this.state.length > 0 ? this.state[i] : []
                    },
                    documentName: documentValFromUI
                  }
                  subject.next(successObject);
                  return subject;
                }
              });
          }
        },
        (err) => {
          console.log('Something went wrong! Err:: ', err);
          this.loader.close();
          this.snack.open('The document is not a valid Passport', 'Cancel', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'center',
          });
          return subject;
        }
      );
    }
    // :: Verify Aadhaar Front ::
    if (
      documentValFromUI == 'Aadhar card' &&
      docSideEnum == '1'
    ) {
      console.log("in side 1")
      this.fileService.uploadAadhaarFront(selectedFile).subscribe(
        (res) => {
          this.loader.close();
          if (typeof res === 'object') {
            console.log('api res -- ', res.data);
            if (!res.data['no'].value) {
              this.snack.open(
                'The document is not a valid Aadhaar card',
                'Cancel',
                {
                  duration: 4000,
                  verticalPosition: 'top',
                  horizontalPosition: 'center',
                }
              );
              return subject;
            }
            this.nameDoc = res.data['name'].value;
            console.log(this.nameDoc, nameFromForm)
            this.dialogService
              .confirmationDialog(this.nameDoc, nameFromForm)
              .subscribe((response) => {
                console.log(response);
                if (response == 'No') {
                  // this.changeValue2_Ocr(docVal, i);
                  var obj = {
                    response: 'No'
                  }
                  subject.next(obj);
                  return subject;
                }
                if (response == 'Yes') {
                  var successObject = {
                    response: 'Yes',
                    data: {
                      nameOnCard: res.data['name'].value,
                      dobArray: res.data['dob'].value,
                      idNumberArray: res.data['no'].value,
                      address: this.addressArray.length > 0 ? this.addressArray[i] : [],
                      pincode: this.pincode.length > 0 ? this.pincode[i] : [],
                      gender: this.gender.length > 0 ? this.gender[i] : [],
                      state: this.state.length > 0 ? this.state[i] : []
                    },
                    documentName: documentValFromUI
                  }
                  console.log('successObject:: ', successObject);
                  subject.next(successObject);
                  return subject;
                }
              });
          }
        },
        (err) => {
          console.log('Something went wrong! Err:: ', err);
          this.loader.close();
          this.snack.open(
            'The document is not a valid Aadhaar card',
            'Cancel',
            {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'center',
            }
          );
          return subject;
        }
      );
    }
    // :: Verify Aadhaar Back ::
    if (
      documentValFromUI == 'Aadhar card' &&
      docSideEnum == '2'
    ) {
      this.fileService.uploadAadhaarBack(selectedFile).subscribe(
        (res) => {
          this.loader.close();
          if (typeof res === 'object') {
            console.log('api res -- ', res.data['address'].value);
            if (!res.data['address'].value == null ) {
              this.snack.open(
                'The document is not a valid Aadhaar card',
                'Cancel',
                {
                  duration: 4000,
                  verticalPosition: 'top',
                  horizontalPosition: 'center',
                }
              );
              return subject;
            }
            this.dialogService
              .confirmationDialog(this.nameDoc, nameFromForm)
              .subscribe((response) => {
                console.log(response);
                if (response == 'No') {
                  // this.changeValue2_Ocr(docVal, i);
                  var obj = {
                    response: 'No'
                  }
                  subject.next(obj);
                  return subject;
                }
                if (response == 'Yes') {

                  var successObject = {
                    response: 'Yes',
                    data: {
                      nameOnCard: [],
                      dobArray: [],
                      idNumberArray: [],
                      address: res.data['address'].value,
                      pincode: res.data['pin'].value,
                      gender: [],
                      state: res.data['state'].value
                    },
                    documentName: documentValFromUI
                  }
                  subject.next(successObject);
                  return subject;
                }
              });
          }
        },
        (err) => {
          console.log('Something went wrong! Err:: ', err);
          this.loader.close();
          this.snack.open(
            'The document is not a valid Aadhaar card',
            'Cancel',
            {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'center',
            }
          );
          return subject;
        }
      );
    }
    return subject;
  }


  private enableNext = new BehaviorSubject<any>([]);
  getenableNext() {
    return this.enableNext.asObservable();
  }
  setenableNext(enableNext) {
    this.enableNext.next(enableNext);
  }
}
