import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-legal-opinion-summary',
  templateUrl: './legal-opinion-summary.component.html',
  styleUrls: ['./legal-opinion-summary.component.scss']
})
export class LegalOpinionSummaryComponent implements OnInit {
  opinion:any;
  assetAreaSize:any;
  mktValOfAsset:any;
  lawyerName:any;
  opinionDate:any;
  valuationDate:any;
  description:any;
  legalDetails:any;
  loanInfo: any;
  loanAccountId: any;

  constructor(public dialogRef: MatDialogRef<LegalOpinionSummaryComponent>, private localstore: LocalStoreService,
    private router: Router, 
    private dataService: DataService,
    private apiService: ApiService,) { }

  ngOnInit(): void {
    this.loanInfo = this.localstore.getItem('LOAN_ACC_ARRAY');
    if(this.loanInfo){
      this.loanAccountId = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.getLegalOpinionDetails();
    }, 10);
  
  }
  goToClose(): void {
    this.dialogRef.close();
  }
  goToEdit() {
    this.dialogRef.close();
   this.dataService.sendNavParam(this.legalDetails);
    this.router.navigate([`/loan/account/list/offer-issue/legal-opinion`]);
  } 
  getLegalOpinionDetails(){
    //this.apiService.getCreditRating(this.loanAccountId).subscribe((accountDetails : any) => {
    this.apiService.getLegalOpinion(112).subscribe((accountDetails : any) => {
      console.log("All Account Service",accountDetails);
      this.legalDetails = accountDetails;
      
      
      });
  }


}
