import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-offer-assessment-summary',
  templateUrl: './offer-assessment-summary.component.html',
  styleUrls: ['./offer-assessment-summary.component.scss']
})
export class OfferAssessmentSummaryComponent implements OnInit {
  requestedLoanAmount:any;
  loanTenure:any;
  systemRecommendation:any;
  userRecommendation:any;
  loanAmountRecommendation:any;
  approvedLoanAmount:any;
  rateOfInterest:any;
  accountDetails:any;
  loanInfo: any;
  loanAccountId: any;

  constructor(public dialogRef: MatDialogRef<OfferAssessmentSummaryComponent>, private localstore: LocalStoreService,
    private router: Router,
    private dataService: DataService,
    private apiService: ApiService,) { }

  ngOnInit(): void {
    this.loanInfo = this.localstore.getItem('LOAN_ACC_ARRAY');
    if(this.loanInfo){
      this.loanAccountId = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.getAccountDetails(); 
    }, 10);

  }
  goToClose(): void {
    this.dialogRef.close();
  }
  goToEdit() {
    this.dialogRef.close();
   // this.dataService.sendNavParam(this.assetValued);
   this.dataService.sendNavParam(this.accountDetails);
    this.router.navigate([`/loan/account/list/offer-issue/offer-assessment`]);
  } 
  getAccountDetails(){
    this.apiService.getAssesmentDetails("520").subscribe((accountDetails : any) => {
      console.log("All Account Service",accountDetails);
      this.accountDetails = accountDetails;
      console.log("this.offerissuedetail",this.accountDetails);
      
      
      });
  }

}
