import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-credit-rating-summary',
  templateUrl: './credit-rating-summary.component.html',
  styleUrls: ['./credit-rating-summary.component.scss']
})

 
export class UnderWritingCreditRatingSummaryComponent implements OnInit {
  creditRatingValues: any;
  loanaccountID: any;
  loanInfo: any;

  

  constructor( public dialogRef: MatDialogRef<UnderWritingCreditRatingSummaryComponent>, private router: Router,
    private ls: LocalStoreService,
     private snack: MatSnackBar,private api: ApiService,  private dataService:DataService) { }
  ngOnInit(): void {
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.gettingCreditRatingDetail();
    }, 10);
   
  }

  goToClose(): void {
    this.dialogRef.close();
  }

  goToEdit() {
    // this.dataService.sendNavParam(this.creditRatingValues); 
    this.dialogRef.close();
    this.router.navigate([`/loan/account/list/underwritingStage/creditrating`]);

    // this.router.navigate([`/home/application/customer-information/${this.productAccountIdCust}`]);
  }
  
  gettingCreditRatingDetail() {
    this.api.gettingCreditRatingDetails(this.loanaccountID)
      .subscribe((creditDetails: any) => {
       console.log(creditDetails);
       this.creditRatingValues=creditDetails;
      //  this.buildCollateralForm(resp,true);
      });
  }
 

}
