import { Component, Input,OnInit } from '@angular/core';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
//import { AccountService } from '../../account.service';
import { Router } from '@angular/router';
import { MatDialogRef } from '@angular/material/dialog';
import { CollateralDetailsComponent } from '../collateral-details.component';


@Component({
  selector: 'app-collateral-details-summmary',
  templateUrl: './collateral-details-summmary.component.html',
  styleUrls: ['./collateral-details-summmary.component.scss']
})
export class CollateralDetailsSummmaryComponent implements OnInit {
  panelOpenState = false;
  loanaccountID : any = 0;
  showEditOption: boolean = false;
  //collateralDetails: any = [];
  collateralDetails: any;
 // active: boolean = false;
  collateralInfoDetails : any;
  collateralDetailsArray : any;
  @Input() public collateralInfo: any;
  appEntrySummaryView: boolean = false;
  noOfCollateral: any;
  coverAvailable: any;
  totalCollateralValue: any;
  utilizedPreviously: any;
  loanInfo: any;


  constructor(  private ls: LocalStoreService,
    private loader: AppLoaderService,
    private apiService: ApiService,
    private router: Router,
    public dialogRef: MatDialogRef<CollateralDetailsComponent>, 
  //  private accountService: AccountService
  ) { }

  ngOnInit(): void {
    
    console.log("collateralInfo",this.collateralInfo);
    this.loanaccountID = this.ls.getItem('loanAccountId');
    console.log("collateralInfo::",this.collateralInfo)
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
   this.loadData(this.collateralInfo);
  //  this.getCollateralDetails();
  }

  
  loadData(collateralInfo){
    if (collateralInfo == null) {
      this.appEntrySummaryView = false;
      setTimeout(() => {
        this.getCollateralDetails();
      }, 10);
     
    } else {
      if (collateralInfo.isAppEntryView) {
        this.appEntrySummaryView = true;
    //  this.collateralDetails.thirdPartyCollateral = true;
     this.collateralDetails = collateralInfo.data;
     console.log("collateralData:",collateralInfo.data)
     this.collateralDetailsArray = collateralInfo.data.collateralDetails;
     console.log("collateralDetailsArray::",collateralInfo.data.collateralDetails);

      } 
    }
    console.log("collateralDetailsArray::",collateralInfo);
  
  }

  


  getCollateralDetails() {
   // this.loader.open();
      this.apiService.getCollateralByLoanAccId(this.loanaccountID).subscribe((collateralDetails) => {
      this.loader.close();
      this.collateralDetails=collateralDetails;
      console.log("collateralDetails::",collateralDetails);
      this.collateralInfo=collateralDetails;
      console.log("collateralDetails:",this.collateralInfo)
      console.log(this.collateralDetails.collateralDetails.length);
      console.log("saved response",collateralDetails);
      this.collateralDetailsArray=collateralDetails.collateralDetails;
      this.loader.close();
    });
  }

 
  stayTrue(){
    this.collateralDetails.thirdPartyCollateral = true;
  }
  stayTrue1(){
    this.collateralDetails.secondaryChargeAllowed = true;
  }
  
  goToClose(): void {
    this.dialogRef.close();
  }
  
  goToEdit() {
    this.dialogRef.close();
     this.router.navigate([`/loan/account/collateral_details`]);
  }

}
