import { Component, OnInit,ViewChild, ChangeDetectorRef, Input } from '@angular/core';
import { STEP_STATE } from '@angular/cdk/stepper';
import { MatDialogRef } from '@angular/material/dialog';
import { MatVerticalStepper } from '@angular/material/stepper';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { Router } from '@angular/router';
import { QualitativeScoreboardSummary } from 'app/shared/models/qualitativeScoreboardSummary';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';


@Component({
  selector: 'app-loan-assessment-view-summary',
  templateUrl: './loan-assessment-view-summary.component.html',
  styleUrls: ['./loan-assessment-view-summary.component.scss']
})
export class LoanAssessmentViewSummaryComponent implements OnInit {
  title: string = "Loan Account Qualitative Scoreboard";
  imgUrl = "assets/images/flagUSA.jpg";
  accountId:any;
  qualitativeScoreboardSummary : any;
  accountDetails: any;
  loanaccountID: any;
  loanAccountId:any;
  showEditOption: boolean = false;
  loanInfo: any;
  constructor(
    public dialogRef: MatDialogRef<LoanAssessmentViewSummaryComponent>,
    private router:Router, 
    private ls: LocalStoreService,
    private apiService: ApiService,
    private dataService: DataService,
  ) { }

  ngOnInit(): void {
    this.loanaccountID = this.ls.getItem('loanAccountId');
    console.log("LOAN ACCOUNT ID:: ",this.loanaccountID);
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanAccountId = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.getAllData();
      this.getAccountDetails();
    }, 10);
  

 
  }
  getAllData(){
    // id == '' || id == null ? id = 1234 : id;
    this.apiService.getLoanQualitativeScorecardDetails(this.loanaccountID).subscribe((response) => {
      this.qualitativeScoreboardSummary = response;
      console.log(this.qualitativeScoreboardSummary);
       console.log(this.qualitativeScoreboardSummary.scoreCardMapping);
    });
  }

  getAccountDetails(){
    this.apiService.fetchLoanAssessment(this.loanaccountID).subscribe((accountDetails : any) => {
      console.log("All Account Service",accountDetails);
      this.accountDetails = accountDetails;
      });
   }

  Close(): void {
    this.dialogRef.close();
  }

  goBack() {
    this.router.navigate([`/loan/account/assessment/list`]);
  }

  done() {
    this.router.navigate([`/loan/account/supervisor/list`]);
  }
  }