import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-kyc-communication-address-update',
  templateUrl: './kyc-communication-address-update.component.html',
  styleUrls: ['./kyc-communication-address-update.component.scss']
})
export class KycCommunicationAddressUpdateComponent implements OnInit {
  addressUpdateForm: FormGroup;
  customerId: any;
  zipIndex: any;
  isAllSameDigitVal: boolean;
  countrys: any = [];
  arrayState: any = [];
  arrayState1: any = [];
  arrayCity1: string[] = [];
  arrayCity: string[] = [];
  array = [
    {
      id: 1,
      checked: true,
      name: "Yes",
    },
    {
      id: 2,
      checked: false,
      name: "No",
    },
  ];
  Residences: string[];
  constructor(
    private dialogRef: MatDialogRef<KycCommunicationAddressUpdateComponent>,
    @Inject(MAT_DIALOG_DATA) private data,
    private formBuilder: FormBuilder,
    private apiService: ApiService,
    private ls: LocalStoreService,
    private snack: MatSnackBar
  ) { }

  ngOnInit(): void {
    this.customerId = this.ls.getItem('customerId')
    console.log(this.data);
    this.getResidenceType();
    this.getAllCountry('country');
    if (this.data) {
      this.buildAddressForm();
      this.getStateName(this.data[0].country)
      this.getCityName(this.data[0].state)
      this.addressUpdateForm.get('communicationAddress').patchValue(this.data[0])
      if (this.data[1]) {
        this.array = [
          {
            id: 1,
            checked: false,
            name: "Yes",
          },
          {
            id: 2,
            checked: true,
            name: "No",
          },
        ];
        this.getPerStateName(this.data[1].country)
        this.getPerCityName(this.data[1].state)
        this.addressUpdateForm.get('isAddressSame').setValue('No')
        this.addressUpdateForm.addControl("permanentAddress", this.addPermanentAddress(this.data[1]))
      }
    }

  }
  buildAddressForm() {
    this.addressUpdateForm = this.formBuilder.group({
      cifNumber: [this.customerId],
      isAddressSame: ["Yes"],
      communicationAddress: this.formBuilder.group({
        addressType: ["Communication", [Validators.required]],
        address1: ["", [Validators.required, Validators.pattern('.*[a-zA-Z].*')]],
        address2: ["", [Validators.required, Validators.pattern('.*[a-zA-Z].*')]],
        city: ["", [Validators.required]],
        zipCode: ["", [Validators.required, Validators.minLength(5), Validators.maxLength(10)]],
        country: ["", [Validators.required]],
        residenceType: ["", [Validators.required]],
        state: ["", [Validators.required]],
      }),

    });
  }
  addPermanentAddress(data?) {
    return this.formBuilder.group({
      addressType: ["Permanent", [Validators.required]],
      address1: [data ? data.address1 : "", [Validators.required, Validators.pattern('.*[a-zA-Z].*')]],
      address2: [data ? data.address2 : "", [Validators.required, Validators.pattern('.*[a-zA-Z].*')]],
      city: [data ? data.city : "", [Validators.required]],
      zipCode: [data ? data.zipCode : "", [Validators.required, Validators.minLength(5), Validators.maxLength(10)]],
      country: [data ? data.country : "", [Validators.required]],
      residenceType: [data ? data.residenceType : "", [Validators.required]],
      state: [data ? data.state : "", [Validators.required]],
    });
  }
  getAllCountry(country) {
    this.apiService.getAllCountries().subscribe((data: any) => {
      for (let i = 0; i < data.length; i++) {
        if (data[i].authStatus === 'A') {
          console.log(data[i])
          this.countrys.push(data[i].countryName);
        }
      }
    });
  }
  getStateName(country, address?) {
    this.apiService.getAllCountries().subscribe((data: any) => {
      for (let i = 0; i < data.length; i++) {
        if (data[i].countryName === country) {
          this.apiService.getStateByCountryId(data[i].id).subscribe((resp: any) => {
            for (let i = 0; i < resp.length; i++) {
              if (resp[i].authStatus === 'A') {
                this.arrayState.push(resp[i].stateName);
              }
            }
          })
          break;
        }
      }
    })
  }
  getPerStateName(country) {
    this.apiService.getAllCountries().subscribe((data: any) => {
      for (let i = 0; i < data.length; i++) {
        if (data[i].countryName === country) {
          this.apiService.getStateByCountryId(data[i].id).subscribe((resp: any) => {
            for (let i = 0; i < resp.length; i++) {
              if (resp[i].authStatus === 'A') {
                this.arrayState1.push(resp[i].stateName);
              }
            }
          })
          break;
        }
      }
    })
  }
  getCityName(state) {
    this.apiService.getAuthorizedCityInfo().subscribe(res => res.map(r => {
      this.arrayCity.push(r.cityName);
    }));
  }
  getPerCityName(state) {
    // this.apiService.getDropDownForStateCity('city', state).subscribe((data) => {
    //   this.arrayCity1 = data;
    // })
    this.apiService.getAuthorizedCityInfo().subscribe(res => res.map(r => {
      this.arrayCity.push(r.cityName);
    }));

  }
  getResidenceType() {
    this.apiService.getDropDownValues('residence').subscribe((responses) => {
      this.Residences = responses;
    });
  }

  onChange(control, e): void {
    if (
      e.value == "No" && !this.addressUpdateForm.contains("permanentAddress")
    ) {
      this.addressUpdateForm.get('isAddressSame').setValue('No');
      this.addressUpdateForm.addControl("permanentAddress", this.addPermanentAddress()
      );
    } else {
      this.addressUpdateForm.get('isAddressSame').setValue('Yes');
      this.addressUpdateForm.removeControl("permanentAddress");
    }
  }
  keyPressAlphanumeric(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[a-zA-Z0-9]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  isAllSameDigit(event, zipIndex) {
    this.zipIndex = zipIndex;
    let count = 0, number = event.target.value;
    if (number != '') {
      for (var i = 0; i < number.length; i++) {
        if (0 == number[i]) {
          count++;
        }
      }
      if (count == number.length) {
        console.log('if loop')
        this.isAllSameDigitVal = true;
      }
      else {
        this.isAllSameDigitVal = false;
      }
    } else {
      this.isAllSameDigitVal = false;
    }
  }
  onSubmit() {
    if (this.addressUpdateForm.invalid) {
      return;
    }

    let userAddress: any[] = [];
    userAddress.push(this.addressUpdateForm.value.communicationAddress);
    if (this.addressUpdateForm.value.permanentAddress) {
      userAddress.push(this.addressUpdateForm.value.permanentAddress);
    }
    let payload = {
      cifNumber: this.addressUpdateForm.value.cifNumber,
      userAddress: userAddress,
      verificationType: 'KYC',
    };

    this.apiService.kycScreenBasicinfo(payload).subscribe((resp) => {
      this.snack.open("Communication Address Updated!", "OK", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });

      this.dialogRef.close({
        status: 'INITIATED',
        resp: userAddress
      })

    });
  }

  close() {
    this.dialogRef.close({
      status: 'UNDER_PROCESS'
    })
  }
}
