import { Component, OnInit,ViewChild, ChangeDetectorRef, Input } from '@angular/core';
import { STEP_STATE } from '@angular/cdk/stepper';
import { MatDialogRef } from '@angular/material/dialog';
import { MatVerticalStepper } from '@angular/material/stepper';
import { ApiService } from 'app/shared/services/api.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-cardaccount-app-entry-stage-view-summary',
  templateUrl: './cardaccount-app-entry-stage-view-summary.component.html',
  styleUrls: ['./cardaccount-app-entry-stage-view-summary.component.scss']
})
export class CardaccountAppEntryStageViewSummaryComponent implements OnInit {
  isLinear = false;
  productAccountId: any;
  accountStatement: any;
  bankPreference: any = [];
  statementPreference: any = [];
  statementPreferenceFetched: any[];
  checkBook: any;
  bankChannel: any;
  commChannel: any = [];
  commChannelFetched: any[];
  passBook: any;
  summaryResponse: any;


  @ViewChild(MatVerticalStepper) stepper: MatVerticalStepper;
  ngAfterViewInit() {
    console.log('in afterview init')
    this.stepper._getIndicatorType = () =>  STEP_STATE.NUMBER; //'number'
  }
  data: any;
  cardInitationResp:any;
  customerInfoResp: any;
  accountServiceResp:any;
  cardInitiationInfo: { isAppEntryView: boolean; data: any; };
  cardCustomerInfo: { isAppEntryView: boolean; data: any; };
  cardFinancialInfo: { isAppEntryView: boolean; data: any; };
  cardPreferenceInfo: { isAppEntryView: boolean; data: any; };
  accountServiceInfo: { isAppEntryView: boolean; data: any; };

  cardId: any;
  isApprovedSreen: any;

  constructor(
    public dialogRef: MatDialogRef<CardaccountAppEntryStageViewSummaryComponent>,
    public appEntryService: ApplicationEntryStageService, private ls: LocalStoreService,private api:ApiService,
    public appApiService: ApplicationEntryStageService
  ) { }

  ngOnInit(): void {
    this.api.getAppViewSummary().subscribe((resp)=>{
      console.log("message",resp);
      this.isApprovedSreen=resp;
 
  });
  this.cardId = this.ls.getItem('cardId');
  console.log("this.cardId", this.cardId);
  this.getCardEntryStageInfo(this.cardId);
  this.getCustomerInfoDetails(this.cardId);
  this.gettingallAccountService();


}

getCardEntryStageInfo(cardId) {
  this.api.getCardApplicationEntrySummary(cardId)
    .subscribe(cardResp => {
      this.data = cardResp;
      if (this.data != null) {
        console.log('data :: ', this.data);
        this.setData(cardResp)
     //this.getCustomerInfoDetails(cardId);
      }
    });
}
getCustomerInfoDetails(cardId) {
  this.appApiService.fetchCardCustomersByCardId(this.cardId,'cardOrigin')
    .subscribe(custResp => {
      console.log('custResp :: ', custResp);
      this.customerInfoResp = custResp;
      if (this.customerInfoResp != null) {
        this.cardCustomerInfo = {
          'isAppEntryView': true,
          'data': this.customerInfoResp.data
        }
      }
      //cardInitiationInfo
      // if (this.customerInfoResp != null) {
      //   this.cardCustomerInfo = {
      //     'isAppEntryView': true,
      //     'data': this.cardCustomerInfo.data
      //   }
      // }
      //cardCustomerInfo
      // this.cdk.markForCheck();
      // this.cdk.detectChanges();
    });
}

gettingallAccountService() {
  this.api.gettingCardAccountServiceDetails(this.cardId).subscribe((allAccountServiceResp: any) => {
    console.log("All Account Service", allAccountServiceResp);

    this.accountServiceResp = allAccountServiceResp;
    if (this.accountServiceResp != null) {
      this.accountServiceInfo = {
        'isAppEntryView': true,
        'data': this.accountServiceResp.data
      }
    }
    // this.accountServiceInfo = {
    //   'isAppEntryView': true,
    //   'data': data.accountServiceInfo
    // }
    this.accountStatement = allAccountServiceResp.statementFrequency;
    this.statementPreferenceFetched = allAccountServiceResp.statementPref;
    for (let index = 0; index < this.statementPreferenceFetched.length; index++) {
      const element = this.statementPreferenceFetched[index];
      if(element.checked) {
        this.statementPreference.push(element.name);
      }
    }
    this.commChannelFetched = allAccountServiceResp.commChannelPref;
    for (let index = 0; index < this.commChannelFetched.length; index++) {
      const element = this.commChannelFetched[index];
      if(element.checked) {
        this.commChannel.push(element.name);
      }
    }
    this.summaryResponse = allAccountServiceResp;

  });
}


setData(data) {
  this.cardInitiationInfo = {
    'isAppEntryView': true,
    'data': data.cardInitiationInfo
  }
  // this.cardCustomerInfo = {
  //   'isAppEntryView': true,
  //   'data': data.cardCustomerInfo
  // }
  this.cardFinancialInfo = {
    'isAppEntryView': true,
    'data': data.cardFinancialInfo
  }
  this.cardPreferenceInfo = {
    'isAppEntryView': true,
    'data': data.cardPreferenceInfo
  }
  // this.accountServiceInfo = {
  //   'isAppEntryView': true,
  //   'data': data.accountServiceInfo
  // }
}

goToClose(): void {
  this.dialogRef.close();
}

 


}