
import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';


import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-offer-issue-summary',
  templateUrl: './offer-issue-summary.component.html',
  styleUrls: ['./offer-issue-summary.component.scss']
})
export class OfferIssueSummaryComponent implements OnInit {
  applicantName:any;
  approvedLoanAmount:any;
  loanTenure:any;
  installmentType:any;
  installmentFrequency:any;
  rateOfInterest:any;
  offerIssueDate:any;
  generateOffer:any;
  accountDetails : any;
  offerissuedetail: any;
  loanaccountID: any;
  loanInfo: any;

  constructor(public dialogRef: MatDialogRef<OfferIssueSummaryComponent>, private localstore: LocalStoreService,
    private router: Router,private ls:LocalStoreService,
    private dataService: DataService,
    private apiService: ApiService,) { }

  ngOnInit(): void {
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.getAccountDetails();
    }, 10);
 
  }
  goToClose(): void {
    this.dialogRef.close();
  }
  goToEdit() {
    this.dialogRef.close();
   // this.dataService.sendNavParam(this.assetValued);
   this.dataService.sendNavParam(this.offerissuedetail);
    this.router.navigate([`/loan/account/list/offer-issue/offerissue`]);
  } 
  getAccountDetails(){
    this.apiService.getOfferIssueDetails(this.loanaccountID).subscribe((offerissuedetail : any) => {
      console.log("All Account Service",offerissuedetail);
      this.offerissuedetail = offerissuedetail;
      console.log("this.offerissuedetail",this.offerissuedetail);
      
      
      });
  }

}