import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-underwriting-credit-summary',
  templateUrl: './underwriting-credit-summary.component.html',
  styleUrls: ['./underwriting-credit-summary.component.scss']
})
export class UnderwritingCreditSummaryComponent implements OnInit {
  creditRatingValues: any;
  cardId:any;
  customerNames: any[]=[];
  customerName:any;
  constructor(public dialogRef: MatDialogRef<UnderwritingCreditSummaryComponent>, private router: Router,
    private ls: LocalStoreService,
     private snack: MatSnackBar,private api: ApiService,  private dataService:DataService) { }

  ngOnInit(): void {
    this.customerName=this.ls.getItem('primaryCustomerName');
    this.cardId=this.ls.getItem('cardId');
    this.getCreditRatingDetail();
  }
  getCreditRatingDetail() {
    this.api.getCardCreditRatingDetails(this.cardId).subscribe((creditDetails: any) => {
    console.log(creditDetails.customerName);
    this.customerName=creditDetails.customerName;
    this.creditRatingValues=creditDetails;
    });
  }
  goToClose(): void {
    this.dialogRef.close();
  }

  goToEdit() {
    // this.dataService.sendNavParam(this.creditRatingValues); 
    this.dialogRef.close();
    this.router.navigate([`/cardaccount/underwritingStage/credit-rating`]);
  }
 
}
