import { NULL_EXPR } from '@angular/compiler/src/output/output_ast';
import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { QualitativeScoreboardSummary } from 'app/shared/models/qualitativeScoreboardSummary';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
   selector: 'app-loan-qualitative-scoreboard-details',
   templateUrl: './loan-qualitative-scoreboard-details.component.html',
   styleUrls: ['./loan-qualitative-scoreboard-details.component.scss']
})
export class LoanQualitativeScoreboardDetailsComponent implements OnInit {
  accountDetails: object;
  loanaccountID: any;
  loanAccountId:any;
  loanInfo: any;

  constructor( 
    public dialogRef: MatDialogRef<LoanQualitativeScoreboardDetailsComponent>,
    private router:Router, 
    private ls: LocalStoreService,
    private apiService: ApiService,
    private dataService: DataService,
    ) { }

  title: string = "Loan Account Qualitative Scoreboard";
  imgUrl = "assets/images/flagUSA.jpg";
  accountId:any;
  qualitativeScoreboardSummary : any;

  ngOnInit(): void {
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanAccountId = this.loanInfo.loanAccountId;
    }
    console.log("LOAN ACCOUNT ID:: ",this.loanaccountID);
    setTimeout(() => {
      this.getAllData(this.loanaccountID);
    }, 10);
  
  }

  getAllData(id){
    // id == '' || id == null ? id = 1234 : id;
    this.apiService.getLoanQualitativeScorecardDetails(this.loanaccountID).subscribe((response) => {
      this.qualitativeScoreboardSummary = response;
      console.log(this.qualitativeScoreboardSummary);
       console.log(this.qualitativeScoreboardSummary.scoreCardMapping);
    });
  }

  Close(): void {
    this.dialogRef.close();
  }

  goAndEdit(){
    console.log(this.qualitativeScoreboardSummary);
    this.dialogRef.close();
   // this.dataService.sendNavParam(this.accountDetails);
    this.dataService.sendNavParam(this.qualitativeScoreboardSummary);
    //this.router.navigate(['/loan/account/assessment/qualitative/', this.accountId]);
    this.router.navigate(['/loan/account/assessment/qualitative']);
   
  }

}


