import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-legal-opinion-summary',
  templateUrl: './legal-opinion-summary.component.html',
  styleUrls: ['./legal-opinion-summary.component.scss']
})

 
export class UnderWritingLegalOpinionSummaryComponent implements OnInit {
  opinionDetailsValue: any;
  loanaccountID: any;
  loanInfo: any;

  constructor(public dialogRef: MatDialogRef<UnderWritingLegalOpinionSummaryComponent>, private router: Router
    ,private api:ApiService, private ls: LocalStoreService,private dataService: DataService) { }
  ngOnInit(): void {
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.gettingLegalOpinionDetail();
    }, 10);
   
  }
  goToClose(): void {
    this.dialogRef.close();
  }
  goToEdit() {
    this.dialogRef.close();
    this.dataService.sendNavParam(this.opinionDetailsValue);
    this.router.navigate([`/loan/account/list/underwritingStage/legalopinion`]);

    // this.router.navigate([`/home/application/customer-information/${this.productAccountIdCust}`]);
  }
  gettingLegalOpinionDetail() {
    this.api.gettingLegalOpinionDetails(this.loanaccountID)
      .subscribe((opinionDetails: any) => {
       console.log(opinionDetails);
       this.opinionDetailsValue=opinionDetails;
      //  this.buildCollateralForm(resp,true);
      });
  }
}
