import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';

import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-credit-rating-summary',
  templateUrl: './credit-rating-summary.component.html',
  styleUrls: ['./credit-rating-summary.component.scss']
})
export class CreditRatingSummaryComponent implements OnInit {
  customerName:any;
  agencyName:any;
  ratings:any;
  remarks:any;
  loanAccountId:any;
  //showEditOption: boolean = false;
  accountDetails: any;
  loanaccountID: any;
  loanInfo: any;

  constructor(public dialogRef: MatDialogRef<CreditRatingSummaryComponent>,
    private apiService:ApiService, private localstore: LocalStoreService,private dataService: DataService,
    private router: Router,) { }

  ngOnInit(): void {
    this.loanaccountID = this.localstore.getItem('loanAccountId');
    this.loanInfo = this.localstore.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.getAccountDetails();
    }, 10);
 
  }
  goToClose(): void {
    this.dialogRef.close();
  }
  goToEdit() {
    this.dialogRef.close();
    this.dataService.sendNavParam(this.accountDetails);
    this.router.navigate([`/loan/account/list/offer-issue/credit-rating`]);
  } 
  getAccountDetails(){
    //this.apiService.getCreditRating(this.loanAccountId).subscribe((accountDetails : any) => {
      this.apiService.gettingCreditRatingDetails(this.loanaccountID)
      .subscribe((creditDetails: any) => {
       console.log(creditDetails);
       this.accountDetails=creditDetails;
      //  this.buildCollateralForm(resp,true);
      });
  }


}