import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-offer-accept-reject-summary',
  templateUrl: './offer-accept-reject-summary.component.html',
  styleUrls: ['./offer-accept-reject-summary.component.scss']
})
export class AcceptOfferAcceptRejectSummaryComponent implements OnInit {

  applicantName:any;
  approvedLoan:any;
  loanTenure:any;
  installmenttype:any;
  installmentFrequency:any;
  rateOfInterest:any;
  approvedLoanAmmount:any;
  loanTenure1:any;
  offerIssuenDate:any;
  generate:any;
  offerDetails: any;
  userRecommendation={'userRecommendation':'Accept'};
  loanaccountID: any;
  loanInfo: any;
  
 
  constructor(private router: Router,
    public dialogRef: MatDialogRef< AcceptOfferAcceptRejectSummaryComponent>,
    private apiService: ApiService,
    private dataService:DataService,
    private ls: LocalStoreService,
    ) { }

  ngOnInit(): void {
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.fetchOfferAcceptRejectDetails(this.loanaccountID);
    }, 10);
   
   
  }

  fetchOfferAcceptRejectDetails(loanaccountID) {
      this.apiService.getofferDetails(loanaccountID)
        .subscribe((data : any)=> {
          this.offerDetails = data;
          console.log(this.offerDetails);
        })
    }

  edit(){
    this.dataService.sendNavParam(this.offerDetails);
    this.dialogRef.close();
    this.router.navigate([`/loan/account/offerstage/offer-accept-reject`]);
    
  }
  close(){
    this.dialogRef.close();
   // this.router.navigate([`/loan/account-approval/list`]);
  }
}

  