import { Component, Input, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-loan-details-summary',
  templateUrl: './loan-details-summary.component.html',
  styleUrls: ['./loan-details-summary.component.scss']
})
export class LoanDetailsSummaryComponent implements OnInit {
  accountDetails: any;
  businessProductName: any;
  accountBranch: any;
  applicationDate: any;
  accountType: any;
  estimatedCost: any;
  customerContribution: any;
  loanAmount: any;
  purposeOfLoan: any;
  showEditOption: boolean = false;
  loanaccountID: any;
  @Input() public loanDetailsInfo: any;
  appEntrySummaryView: boolean = false;
  loanInfo: any;

  constructor(public dialogRef: MatDialogRef<LoanDetailsSummaryComponent>, private router: Router,
    private dataService: DataService, private ls: LocalStoreService,
    private apiService: ApiService,) { }

  ngOnInit(): void {
    console.log("loanDetailsInfo :: ", this.loanDetailsInfo);
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    console.log("this.loanaccountID", this.loanaccountID);
    setTimeout(() => {
      this.loadData(this.loanDetailsInfo);
    }, 1);

  }

  loadData(loanDetailsInfo){
    if (loanDetailsInfo == null) {
      this.appEntrySummaryView = false;
      this.getLoanDetails();
    } else {
      if (loanDetailsInfo.isAppEntryView) {
        this.appEntrySummaryView = true;
        this.accountDetails = loanDetailsInfo.data;
      }
    }
  }
  goToEdit() {
    this.dialogRef.close();
    this.dataService.sendNavParam(this.accountDetails);
    this.router.navigate([`loan/account/loan-details`]);
  }
  goToClose(): void {
    this.dialogRef.close();
  }
  getLoanDetails() {
    //this.apiService.getAssetValuation(this.loanAccountId).subscribe((accountDetails : any) => {
    this.apiService.getLoanDetails(this.loanaccountID).subscribe((accountDetails: any) => {
      console.log("All Account Service", accountDetails);
      this.accountDetails = accountDetails;

      console.log("this.accountDetails", this.accountDetails);


    });
  }
}
