import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-account-service-summary',
  templateUrl: './account-service-summary.component.html',
  styleUrls: ['./account-service-summary.component.scss']
})
export class AccountServiceSummaryComponent implements OnInit {
  title = "Account Service Details";
  // productAccountId = 1975;
  productAccountId: any;
  accountStatement: any;
  bankPreference: any = [];
  statementPreference: any = [];
  statementPreferenceFetched: any[];
  checkBook: any;
  bankChannel: any;
  commChannel: any = [];
  commChannelFetched: any[];
  passBook: any;
  debitCardRequired: any;
  summaryResponse: any;
  accountInfo: any;
  debitCardType: any;
  constructor(private router: Router, private apiService: ApiService, private localstore: LocalStoreService,
    public dialogRef: MatDialogRef<AccountServiceSummaryComponent>, private dataService: DataService) { }

  ngOnInit(): void {
    this.productAccountId = this.localstore.getItem('accountId');
    this.accountInfo = this.localstore.getItem('EDIT-CREATEACCCIF');
    if(this.accountInfo){
      console.log("Acc",this.accountInfo)
      this.productAccountId=this.accountInfo.businessProductAccount.accountId
    }
    console.log("Account ID from Local Storage", this.productAccountId);
    this.gettingallAccountService(this.productAccountId);
  }
  goToClose() {
    this.dialogRef.close();
  }
  gettingallAccountService(productAccountId) {
    this.apiService.gettingAccountServiceDetails(this.productAccountId).subscribe((allAccountServiceResp: any) => {
      console.log("All Account Service", allAccountServiceResp);
      this.accountStatement = allAccountServiceResp.statementFrequency;
      this.checkBook = allAccountServiceResp.chequebookNeeded;
      this.bankChannel = allAccountServiceResp.bankChannelPref;
      this.debitCardType = allAccountServiceResp.debitCardType;
      for (let index = 0; index < this.bankChannel.length; index++) {
        const element = this.bankChannel[index];
        if(element.checked) {
          this.bankPreference.push(element.name);
        }
      }

      this.passBook = allAccountServiceResp.passbookNeeded;
      this.debitCardRequired = allAccountServiceResp.debitCardRequired;
      this.statementPreferenceFetched = allAccountServiceResp.statementPref;
      for (let index = 0; index < this.statementPreferenceFetched.length; index++) {
        const element = this.statementPreferenceFetched[index];
        if(element.checked) {
          this.statementPreference.push(element.name);
        }
      }
      this.commChannelFetched = allAccountServiceResp.commChannelPref;
      for (let index = 0; index < this.commChannelFetched.length; index++) {
        const element = this.commChannelFetched[index];
        if(element.checked) {
          this.commChannel.push(element.name);
        }
      }
      this.summaryResponse = allAccountServiceResp;

    });
  }
  goToEdit() {
    this.dialogRef.close();
    this.dataService.sendNavParam(this.summaryResponse);
    this.router.navigate([`/home/application/account-service/${this.productAccountId}`]);
  }

}
