import { Component, Input, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-vehicle-details-summary',
  templateUrl: './vehicle-details-summary.component.html',
  styleUrls: ['./vehicle-details-summary.component.scss']
})
export class VehicleDetailsSummaryComponent implements OnInit {
  vehicleDetails:any;
  insuranceDetails:any;
  distanceRun:any;
  engineNumber:any;
  expectedSellingPrice:any;
  insuranceCompany:any;
  model:any;
  policyCommencementDate:any;
  policyNumber:any;
  policyRenewalDate:any;
  premiunAmount:any;
  premiunFrequency:any;
  productName:any;
  registrationCity:any;
  registrationNumber:any;
  registrationState:any;
  vehicleClass:any;
  chassisNumber:any;
  loanaccountID: any;
  @Input() public vehicleInfo: any;
  appEntrySummaryView: boolean = false;
  loanInfo: any;



  constructor(public dialogRef: MatDialogRef<VehicleDetailsSummaryComponent>, private router: Router,
    private dataService: DataService, private ls: LocalStoreService,
    private apiService: ApiService,) { }

  ngOnInit(): void {
    this.loanaccountID = this.ls.getItem('loanAccountId');
    console.log("this.productaccountID1",this.loanaccountID);
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    this.loadData(this.vehicleInfo)
  }
  loadData(vehicleInfo) {
    if (vehicleInfo == null) {
      this.appEntrySummaryView = false;
      setTimeout(() => {
        this.getVehicleDetails();
      }, 10);
      
    } else {
      if (vehicleInfo.isAppEntryView) {
        this.appEntrySummaryView = true;
        this.vehicleDetails = vehicleInfo.data;
      }
    }
  }
  goToEdit(){
    this.dialogRef.close();
    this.dataService.sendNavParam(this.vehicleDetails);
    this.router.navigate([`loan/account/vehicle-details`]);
  }
  goToClose(): void {
    this.dialogRef.close();
  }
  getVehicleDetails(){
    //this.apiService.getAssetValuation(this.loanAccountId).subscribe((accountDetails : any) => {
    this.apiService.getVehicleDetails(this.loanaccountID).subscribe((vehicleDetails : any) => {
      console.log("All Account Service",vehicleDetails);
      this.vehicleDetails = vehicleDetails;
     
      console.log("this.vehicleDetails",this.vehicleDetails);
      
      
      });
  }
}
