import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AppointmentComponent } from 'app/views/others/video-verification/appointment/appointment.component';
import { IcustCustomerVideoVerification } from 'app/views/others/video-verification/IcustCustomerVideoVerification';
import { VideoService } from 'app/views/others/video-verification/video.service';
import * as moment from 'moment';

@Component({
  selector: 'app-kyc-appointment-schedule-update',
  templateUrl: './kyc-appointment-schedule-update.component.html',
  styleUrls: ['./kyc-appointment-schedule-update.component.scss']
})
export class KycAppointmentScheduleUpdateComponent implements OnInit {
  maxDate = new Date();
  minDate = new Date();
  kycTimeOption = false;
  TimeSlots = [];
  dateCollection: any[];
  showNext: boolean;
  count: any= 0;
  datewitmAmPm: string;
  showSpinn: boolean;
  selectedDate: any;
  tellerId = 1;
  icustCustVideoVerifyDto: IcustCustomerVideoVerification;
  loggedInUser = 1;
  kycRef: any;
  link: any;
  customerId: any;
  data:any;
  schedule: any;
  selectedTime:any;
  index: any;

  constructor(
    @Inject(MAT_DIALOG_DATA) private videoVerifcationDetails,
    private dialogRef: MatDialogRef<KycAppointmentScheduleUpdateComponent>,
    private apiService: ApiService,
    private ls: LocalStoreService,
    private formBuilder: FormBuilder,
    private videoService: VideoService,
    private dialog: MatDialog
  ) { 
    this.maxDate.setDate(this.maxDate.getDate() + 7);
  }

  ngOnInit(): void {
    console.log(this.videoVerifcationDetails);
    this.data = this.videoVerifcationDetails.data;
    this.schedule = this.videoVerifcationDetails.schedule
    this.customerId = this.ls.getItem('customerId')
    this.callingTimeslots();
    this.link = "vide_link";
    if(this.schedule){
      console.log(this.selectedTime, this.TimeSlots[1].time);
      
      this.selectedDate = this.schedule[0].scheduledDate;
      this.selectedTime = this.schedule[0].scheduledTime[0];
      this.onSelect(this.selectedDate);
      for(let i = 0; i < this.TimeSlots.length; i++){
        if(this.TimeSlots[i].time == this.selectedTime)
      this.onSelectTime1(this.selectedTime,i,{'target.innerText':this.selectedTime})
      break;
      }
    }

  }
  callingTimeslots() {
    this.TimeSlots = [
      {
         "time":"9:00",
         "available":true,
         "selected":false,
         "booked":false
      },
      {
         "time":"9:30",
         "available":true,
         "selected":false,
         "booked":false
      },
      {
         "time":"10:00",
         "available":true,
         "selected":false,
         "booked":false
      },
      {
         "time":"10:30",
         "available":true,
         "selected":false,
         "booked":false
      },
      {
         "time":"11:00",
         "available":true,
         "selected":false,
         "booked":false
      },
      {
         "time":"11:30",
         "available":true,
         "selected":false,
         "booked":false
      },
      {
         "time":"12:00",
         "available":true,
         "selected":false,
         "booked":false
      },
      {
         "time":"12:30",
         "available":true,
         "selected":false,
         "booked":false
      },
      {
         "time":"13:00",
         "available":true,
         "selected":false,
         "booked":false
      },
      {
         "time":"13:30",
         "available":true,
         "selected":false,
         "booked":false
      },
      {
         "time":"14:00",
         "available":true,
         "selected":false,
         "booked":false
      },
      {
         "time":"14:30",
         "available":true,
         "selected":false,
         "booked":false
      },
      {
         "time":"15:00",
         "available":true,
         "selected":false,
         "booked":false
      },
      {
         "time":"15:30",
         "available":true,
         "selected":false,
         "booked":false
      },
      {
         "time":"16:00",
         "available":true,
         "selected":false,
         "booked":false
      },
      {
         "time":"16:30",
         "available":true,
         "selected":false,
         "booked":false
      },
      {
         "time":"17:00",
         "available":true,
         "selected":false,
         "booked":false
      }
   ];
  }
  onSelect(event) {
    console.log(event);
    
    for(let i=0;i<this.TimeSlots.length;i++){
    var slot=  this.bookSlot(this.format24HrsTo12Hrs(this.TimeSlots[i].time));
    this.TimeSlots[i].booked=slot;
    }
    this.callingTimeslots();
    this.showSpinn = true;
    this.dateCollection = [];
    console.log(event);
    this.selectedDate = this.videoService.convertDate(event);
    this.apiService.gettingAvailableSlotsTeller(this.selectedDate, this.tellerId).subscribe((availableSlotsResp: any) => {
      if (availableSlotsResp) {
        console.log(availableSlotsResp);
        
        this.showSpinn = false;
        this.kycTimeOption = true;
        availableSlotsResp.bookedSlots.forEach(element => {
          const index = this.TimeSlots.map(x => x.time).indexOf(element);
          if (index > -1) {
            this.TimeSlots[index].available = false;
            this.TimeSlots[index].booked = true;
          }
        });

      }
    });
  }
  format24HrsTo12Hrs(time){
    var formatted = moment(time, "HH:mm").format("LT");
    return formatted;
  }
  bookSlot(bookedTime){
    var now=new Date(); 
    var time = bookedTime;
    var dt = (now.getMonth()+1) + "/" + now.getDate() + "/" + now.getFullYear() + " " + time;
    var userval = new Date(dt);
    if (now > userval) {
    return false;
   }else{
  return true;
   }
  }

  onSelectTime1(time, i,event) {
    this.showNext = true;
console.log(event)
    this.datewitmAmPm=event.target.innerText;
    this.count++;
    if (this.count > 1) {
      this.dateCollection = [];
      console.log(!this.TimeSlots[i].selected);
      this.showNext =!this.TimeSlots[i].selected ;
    }
    console.log("Time", time, i);

    this.TimeSlots[i].selected = !this.TimeSlots[i].selected;
    this.TimeSlots[i].available = !this.TimeSlots[i].available;
    console.log(this.TimeSlots[i].selected);
    const index = this.dateCollection.indexOf(time);
    if (index > -1 && !this.TimeSlots[i].selected) {
      this.dateCollection.splice(index, 1);
    }
    else if (index == -1 && this.TimeSlots[i].selected) {
      this.dateCollection.push(time);
    }
    
  }
  onSubmit(){
    this.icustCustVideoVerifyDto = this.data
    this.icustCustVideoVerifyDto.scheduledDate = this.selectedDate;
    this.icustCustVideoVerifyDto.scheduledTime = this.dateCollection;
    this.icustCustVideoVerifyDto.tellerId = this.tellerId;
    this.icustCustVideoVerifyDto.loggedInUser = this.loggedInUser;
    this.videoService.saveCustBasicInfoData(this.icustCustVideoVerifyDto).subscribe(data => {
      this.link = data;
      const dialogRef = this.dialog.open(AppointmentComponent, {

        data: { time:  this.datewitmAmPm, date: this.selectedDate, link: this.link, kycRef: this.data.kycReference }

      });

      dialogRef.afterClosed().subscribe(result => {
        console.log('The dialog was closed');
        // this.animal = result;
      });
  })
}
  close() {
    this.dialogRef.close({
      status: 'UNDER_PROCESS'
    })
  }
}
