
import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-loan-assessment-details-summary',
  templateUrl: './loan-assessment-details-summary.component.html',
  styleUrls: ['./loan-assessment-details-summary.component.scss']
})
export class LoanAssessmentDetailsSummaryComponent implements OnInit
{

  showEditOption: boolean = false;
  accountDetails: any;
  loanaccountID: any;
  loanInfo: any;
  constructor(public dialogRef: MatDialogRef<LoanAssessmentDetailsSummaryComponent>, private localstore: LocalStoreService,
    private router: Router, 
    private dataService: DataService,
    private apiService: ApiService,) { }

    ngOnInit(): void {
      this.loanaccountID = this.localstore.getItem('loanAccountId');
      console.log("loanAccountId::",this.loanaccountID);
      this.loanInfo = this.localstore.getItem('LOAN_ACC_ARRAY');
      console.log(this.loanInfo);
      if(this.loanInfo){
        this.loanaccountID = this.loanInfo.loanAccountId;
      }
      setTimeout(() => {
        this.getAccountDetails();
      }, 10);
  
    }
  goToClose(): void {
    this.dialogRef.close();
  }
  goToEdit() {
    this.dialogRef.close();
   this.router.navigate([`/loan/account/assessment/assessment-details`]);
   this.getAccountDetails();
  } 
  getAccountDetails(){
    this.apiService.fetchLoanAssessment(this.loanaccountID).subscribe((accountDetails : any) => {
      console.log("All Account Service",accountDetails);
      this.accountDetails = accountDetails;
      });
   }

}