import { NULL_EXPR } from '@angular/compiler/src/output/output_ast';
import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { QualitativeScoreboardSummary } from 'app/shared/models/qualitativeScoreboardSummary';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-report-details',
  templateUrl: './report-details.component.html',
  styleUrls: ['./report-details.component.scss']
})
export class ReportDetailsComponent implements OnInit {
  accountInfo: any;

  constructor( 
    public dialogRef: MatDialogRef<ReportDetailsComponent>,
    private router:Router, 
    private ls: LocalStoreService,
    private apiService: ApiService,
    private dataService: DataService,
    ) { }

  title: string = "Qualitative Scoreboard";
  imgUrl = "assets/images/flagUSA.jpg";
  accountId:any;
  qualitativeScoreboardSummary : any;

  ngOnInit(): void {
    this.accountId = this.ls.getItem('accountId');
    this.accountInfo = this.ls.getItem('EDIT-CREATEACCCIF');
    if(this.accountInfo){
      this.accountId=this.accountInfo.businessProductAccount.accountId
    }
    console.log(this.accountId,"AccountId");
    setTimeout(() => {
      this.getAllData(this.accountId);
    }, 1);
 
  }

  getAllData(id){
    // id == '' || id == null ? id = 1234 : id;
    this.apiService.getQualitativeScorecardDetails(id).subscribe((response) => {
      this.qualitativeScoreboardSummary = response;
      console.log(this.qualitativeScoreboardSummary);
       console.log(this.qualitativeScoreboardSummary.scoreCardMapping);
    });
  }

  Close(): void {
    this.dialogRef.close();
  }

  goAndEdit(){
    console.log(this.qualitativeScoreboardSummary);
    this.dialogRef.close();
    this.dataService.sendNavParam(this.qualitativeScoreboardSummary);
    this.router.navigate(['/home/application/assessment/qualitative/', this.accountId]);
   
  }

}
