import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { OfferIssueDetail } from 'app/shared/models/OfferIssueDetails';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-offer-issue-summary',
  templateUrl: './offer-issue-summary.component.html',
  styleUrls: ['./offer-issue-summary.component.scss']
})
export class AcceptOfferIssueSummaryComponent implements OnInit {

  applicantName:any;
  approvedLoanAmount:any;
  loanTenure:any;
  installmentType:any;
  installmentFrequency:any;
  rateOfInterest:any;
  offerIssueDate:any;
  generateOffer:any;
  accountDetails : OfferIssueDetail;
  offerissuedetail: any[];
  loanInfo: any;
  loanaccountID: any;

  constructor(public dialogRef: MatDialogRef<AcceptOfferIssueSummaryComponent>, private localstore: LocalStoreService,
    private router: Router,
    private dataService: DataService,
    private apiService: ApiService,) { }

  ngOnInit(): void {
    this.loanInfo = this.localstore.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.getAccountDetails();
    }, 10);
   
  }
  goToClose(): void {
    this.dialogRef.close();
  }
  goToEdit() {
    this.dialogRef.close();
   // this.dataService.sendNavParam(this.assetValued);
   this.dataService.sendNavParam(this.accountDetails);
   this.router.navigate([`/loan/account/offerstage/offer-issue`]);
  } 
  getAccountDetails(){
    this.apiService.getOfferIssueDetails(this.loanaccountID ? this.loanaccountID :44444).subscribe((accountDetails : any) => {
      console.log("All Account Service",accountDetails);
      this.accountDetails = accountDetails;
      console.log("this.offerissuedetail",this.offerissuedetail);
      
      
      });
  }

}
