import { STEP_STATE } from '@angular/cdk/stepper';
import { Component, OnInit, ViewChild, ChangeDetectorRef, Input } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { MatVerticalStepper } from '@angular/material/stepper';
import { ApiService } from 'app/shared/services/api.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-loan-app-entry-stage-view-summary',
  templateUrl: './loan-app-entry-stage-view-summary.component.html',
  styleUrls: ['./loan-app-entry-stage-view-summary.component.scss']
})
export class LoanAppEntryStageViewSummaryComponent implements OnInit {
  isLinear = false;


  @ViewChild(MatVerticalStepper) stepper: MatVerticalStepper;
  loanInfo: any;
  loantype: any;
  ngAfterViewInit() {
    console.log('in afterview init')
    this.stepper._getIndicatorType = () =>  STEP_STATE.NUMBER; //'number'
  }
  data: any;
  customerInfoResp: any;
  loanDetailsInfo: { isAppEntryView: boolean; data: any; };
  assetInfo: { isAppEntryView: boolean; data: any; };
  vehicleInfo: { isAppEntryView: boolean; data: any; };
  admissionInfo: { isAppEntryView: boolean; data: any; };
  mandateInfo: { isAppEntryView: boolean; data: any; };
  financialInfo: { isAppEntryView: boolean; data: any; };
  collateralInfo: { isAppEntryView: boolean; data: any; };
  guarantorInfo: { isAppEntryView: boolean; data: any; };
  customerInfo: { isAppEntryView: boolean; data: any; };
  loanaccountID: any;
  isApprovedSreen: any;



  constructor(public dialogRef: MatDialogRef<LoanAppEntryStageViewSummaryComponent>,
    public appEntryService: ApplicationEntryStageService, private ls: LocalStoreService,private api:ApiService,
    public appApiService: ApplicationEntryStageService
    // private cdk: ChangeDetectorRef
    ) { }

  ngOnInit(): void {
    this.api.getApprovedStatus().subscribe((resp)=>{
      console.log("message",resp);
      this.isApprovedSreen=resp;
 
    })
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    console.log("this.loanaccountID", this.loanaccountID);
    setTimeout(() => {
      this.getLoanEntryStageInfo(this.loanaccountID);
    }, 10);
    
    
    
  }

  getLoanEntryStageInfo(loanaccountID) {
    this.appEntryService.getLoanEntryStageInfo(loanaccountID)
      .subscribe(loanResp => {
        this.loantype = loanResp.loanInfo.accountType;
        this.data = loanResp;
        if (this.data != null) {
          console.log('data :: ', this.data);
          this.setData(loanResp)
          this.fetchCustomersByLoanAccIdForViewSummary(loanaccountID);
        }
      });
  }
  fetchCustomersByLoanAccIdForViewSummary(loanaccountID) {
    this.appApiService.fetchCustomersByLoanAccId(this.loanaccountID,'loan')
      .subscribe(custResp => {
        console.log('custResp :: ', custResp);
        this.customerInfoResp = custResp;
        if (this.customerInfoResp != null) {
          this.customerInfo = {
            'isAppEntryView': true,
            'data': this.customerInfoResp.data
          }
        }
        // this.cdk.markForCheck();
        // this.cdk.detectChanges();
      });
  }


  setData(data) {
    this.loanDetailsInfo = {
      'isAppEntryView': true,
      'data': data.loanInfo
    }
    this.assetInfo = {
      'isAppEntryView': true,
      'data': data.assetInfo
    }
    this.vehicleInfo = {
      'isAppEntryView': true,
      'data': data.vehicleInfo
    }
    this.admissionInfo = {
      'isAppEntryView': true,
      'data': data.admissionInfo
    }
    this.mandateInfo = {
      'isAppEntryView': true,
      'data': data.mandateInfo
    }
    this.financialInfo = {
      'isAppEntryView': true,
      'data': data.financialInfo
    }
    this.collateralInfo = {
      'isAppEntryView': true,
      'data': data.collateralInfo
    }
    this.guarantorInfo = {
      'isAppEntryView': true,
      'data': data.guarantorInfo
    }
  }

  goToClose(): void {
    this.dialogRef.close();
  }

}
