import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { log } from 'console';
import { ApiService } from 'app/shared/services/api.service';
import { KycScreeningNewcustomerComponent } from '../kyc-screening/kyc-screening-newcustomer/kyc-screening-newcustomer.component';
import { VerificationCompletedComponent } from '../verification-completed/verification-completed.component';

@Component({
  selector: 'app-phonenumber-details-popup',
  templateUrl: './phonenumber-details-popup.component.html',
  styleUrls: ['./phonenumber-details-popup.component.scss']
})
export class PhonenumberDetailsPopupComponent implements OnInit {
  customerData: any;
  firstScreen:boolean= true;
  secondScreen:boolean=false;
  // thirdScreen:boolean=false;
  shownextarrow:boolean = true;
  arrayRelationshipType: any[] = [];


  constructor(
    public dialogRef: MatDialogRef<PhonenumberDetailsPopupComponent>,
    @Inject(MAT_DIALOG_DATA) private data: any,
    private apiService: ApiService,
    private dialog:MatDialog
  ) {
    this.customerData=data;
   }

  ngOnInit(): void {
    console.log("resp::",this.customerData);
    this.customerData.map(customer => {
      customer.relationshipType = '';
      customer.remark = '';
    })
    this.getRelationShipType("relationship_type");
  }


  Continue(action,customerData) {
    console.log(this.customerData)
    var payload={
      phoneNumber:customerData.phoneNumber,
      primaryEmailAdress:customerData.primaryEmailAdress,
      address:customerData.address ,
      relationship:customerData.relationshipType,
      remarks:customerData.remark,
      isReKyc: action=='accept'? 'false' : 'true',
    }
    if(action=='accept' || action=='submit'){
    this.apiService.updateKyc(payload).subscribe((resp)=>{
      console.log("resp=------>", resp)
    })
    }


    this.dialogRef.close({
      customerId:customerData.customerId,
      isDialogClose:true
    });
  }

  Cancel(): void {
    this.dialogRef.close("No");
  }

  arrow(){
    this.firstScreen=false;
    this.secondScreen=true;
  }
  arrowBack(){
    this.firstScreen=true;
    this.secondScreen=false;
  }
  callingSecond(evt){
    this.shownextarrow = evt.target.children[0].checked;
    
    console.log(evt.target.children[0].checked);
  }
  getRelationShipType(key: string){
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayRelationshipType = data;
      // console.log("-->", this.arrayCountry)
    });
  }
  Rekyc(){
    const dialogRef = this.dialog.open(KycScreeningNewcustomerComponent,{
    data:{rekyc:true,customerId:this.customerData[0].cifNumber},
    width: '720px', 
    disableClose: true, 
    panelClass: "custom"
    })
      dialogRef.afterClosed().subscribe(res => {
        if(res != 'skip'){
          const verficationDialog = this.dialog.open(VerificationCompletedComponent,{
            data:{rekyc:false,popupClose:true},
            disableClose: true,
            width:'40%'
          })
          verficationDialog.afterClosed().subscribe(done => {
            this.dialogRef.close("Yes")
          })
        }
        else{
          this.dialogRef.close("Yes")
        }
          })
  }

}
