import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';


import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-asset-valuation-summary',
  templateUrl: './asset-valuation-summary.component.html',
  styleUrls: ['./asset-valuation-summary.component.scss']
})
export class AssetValuationSummaryComponent implements OnInit {
  bankValuation:any;
  assetType:any;
  valuationDate:any;
  assetValue:any;
  actualPropertyAreaInUnits:any;
  propertyAreaSize:any;
  actualPropertyAreaSize:any;
  mktValOfAsset:any;
  accountDetails : any;
  loanInfo: any;
  loanaccountID: any;
  


  constructor(public dialogRef: MatDialogRef<AssetValuationSummaryComponent>, private localstore: LocalStoreService,
    private router: Router,
    private dataService: DataService,
    private apiService: ApiService,) { }

  ngOnInit(): void {
    this.loanInfo = this.localstore.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.getAccountDetails();
    }, 10);
    
  }
  goToClose(): void {
    this.dialogRef.close();
  }
  goToEdit() {
    this.dialogRef.close();
    this.dataService.sendNavParam(this.accountDetails);
    this.router.navigate([`/loan/account/list/offer-issue/valuation-asset`]);
  } 
  getAccountDetails(){
    //this.apiService.getAssetValuation(this.loanAccountId).subscribe((accountDetails : any) => {
    this.apiService.getAssetValuation(112).subscribe((accountDetails : any) => {
      console.log("All Account Service",accountDetails);
      this.accountDetails = accountDetails;
      console.log("this.accountDetails",this.accountDetails);
      
      
      });
  }


}