import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { truncateSync } from 'fs';


@Component({
  selector: 'app-confirm-dialog',
  templateUrl: './confirm-dialog.component.html',
  styleUrls: ['./confirm-dialog.component.scss']
})
export class ConfirmDialogComponent implements OnInit {

  Title : string = "Confirmation!";
  Message : string;
  nameInUpload: any;
  nameInBasicTab: any;
  isnameDiff: boolean=false;
  constructor(
    public dialogRef: MatDialogRef<ConfirmDialogComponent>,
    @Inject(MAT_DIALOG_DATA) private data: any
  ) {
    console.log(data);
    this.nameInUpload =data.nameInUpload;
    this.nameInBasicTab=data.nameInBasicTab;
   }

  ngOnInit(): void {

      if( (this.nameInUpload.toLowerCase())!=(this.nameInBasicTab.toLowerCase())){
      this.isnameDiff=true;
    }
    else{
      this.isnameDiff=false;
    }
   
  }

  Close(): void {
    this.dialogRef.close("No");
  }

  Yes(): void {
    this.dialogRef.close("Yes");
  }

}
