import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-card-approval-view-summary',
  templateUrl: './card-approval-view-summary.component.html',
  styleUrls: ['./card-approval-view-summary.component.scss']
})
export class CardApprovalViewSummaryComponent implements OnInit {

  cardId: any;
  approvalDetails: any;
  accountBranch: any;
  accountType: any
  productCode: any;
  productName: any;
  accountCurrency: any;
  existingValues: any;
  userRecommendation: any;
  remarks: any;

  constructor(public dialogRef: MatDialogRef<CardApprovalViewSummaryComponent>,private ls: LocalStoreService,
    private apiService:ApiService,private dataService: DataService,private router: Router) { }

  ngOnInit(): void {
    this.cardId=this.ls.getItem('cardId');
    this.getApprovalDetails();
  }

  goToClose(): void {
    this.dialogRef.close();
  }
  getApprovalDetails(){

    this.apiService.getApprovalDetails(this.cardId)
    .subscribe((resp: any) => {
      console.log("data",resp);
      this.approvalDetails=resp;

     
    });
  }
  goToEdit() {
    this.dialogRef.close();
   // this.dataService.sendNavParam(this.assetValued);
   this.dataService.sendNavParam(this.approvalDetails);
   this.router.navigate(["/cardaccount/SupervisorApprovalStage/list/approvalDetails"]);
  } 

}
