import { Component, Input, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-asset-details-summary',
  templateUrl: './asset-details-summary.component.html',
  styleUrls: ['./asset-details-summary.component.scss']
})
export class AssetDetailsSummaryComponent implements OnInit {
  assetDetails: any
  assetStatus: any;
  homeType: any;
  marketValue: any;
  building: any;
  locality: any;
  street: any;
  city: any;
  state: any;
  zipCode: any;
  country: any;
  dimensions: any;
  loanaccountID: any;
  @Input() public assetInfo: any;
  appEntrySummaryView: boolean = false;
  loanInfo: any;

  constructor(public dialogRef: MatDialogRef<AssetDetailsSummaryComponent>,
    private dataService: DataService, private router: Router, private ls: LocalStoreService,
    private apiService: ApiService) { }

  ngOnInit(): void {
    console.log('assetInfo :: ', this.assetInfo);
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    console.log("this.productaccountID", this.loanaccountID);
    setTimeout(() => {
      this.loadData(this.assetInfo)
    }, 10);
  
  }
  loadData(assetInfo) {
    if (assetInfo == null) {
      this.appEntrySummaryView = false;
      this.getAssetDetails();
    } else {
      if (assetInfo.isAppEntryView) {
        this.appEntrySummaryView = true;
        this.assetDetails = assetInfo.data;
      }
    }
  }
  getAssetDetails() {
    //this.apiService.getAssetValuation(this.loanAccountId).subscribe((accountDetails : any) => {
    this.apiService.getAssetails(this.loanaccountID).subscribe((assetDetails: any) => {
      console.log("All Account Service", assetDetails);
      this.assetDetails = assetDetails;
      console.log("this.assetDetails", this.assetDetails);
    });

  }

  goToEdit() {
    this.dialogRef.close();
    this.dataService.sendNavParam(this.assetDetails);
    this.router.navigate([`loan/account/asset-details`]);
  }
  goToClose(): void {
    this.dialogRef.close();
  }
}
