import { FingerPrintCaptureService } from './../../../shared/services/finger-print-capture.service';
import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { API_URL } from 'app/shared/services/finger-print-capture.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { NewScanComponent } from '../new-scan/new-scan.component';

@Component({
  selector: 'app-kyc-face-scan',
  templateUrl: './kyc-face-scan.component.html',
  styleUrls: ['./kyc-face-scan.component.scss']
})
export class KycFaceScanComponent implements OnInit {

  public customerId: string;
  isScanned:boolean=false;
  refreshedCustId: any;
  isFaliure: boolean;
  faceImage: any;
  constructor(public dialogRef: MatDialogRef<KycFaceScanComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, private snack: MatSnackBar,
    private fingerPrintService: FingerPrintCaptureService,
    private ls:LocalStoreService,private dialog:MatDialog) { }

  ngOnInit(): void {
    this.customerId = sessionStorage.getItem('customer_id');
    if(this.data.customerId)
    this.customerId =  this.data.customerId;
    this.refreshedCustId=this.data.customerId;
    console.log('customer id :: ', this.customerId ,  this.refreshedCustId);
  }

  closeClick(isScanned) {
    console.log("close click")
    // [routerLink]="['/others/kyc']" 
    this.dialogRef.close({
      message: 'Confirm',
      isScanned:isScanned,
      image:this.faceImage
    });
    // this.router.navigateByUrl('/others/kyc'); 
  }

  scan() {
    const dialogRef = this.dialog.open(NewScanComponent,{
      data: {},
      panelClass:'my-custom-dialog-class',
      width: '100%',
      height: '100%',
      backdropClass: 'dialog-bg-trans',
      disableClose: true,
    })
    dialogRef.afterClosed().subscribe(res => {
      console.log(res)
      if(res == 'close'){
        this.dialogRef.close('close');
        return;
      }
      if(res.resImage){
        this.faceImage = res.resImage
        this.isScanned = true;
        this.isFaliure = false;
      }
      else{
        this.isFaliure = true;
        this.isScanned = false;
      }
      
    }
    )
    
    // const obj = {
    //   fileUrl: `${API_URL}/rest/upload/downloadDocs/?fileName=face_biometric.jpg`
    // };
    // this.saveBiometric(obj, 'FACE', this.refreshedCustId);
  }

  saveBiometric(capFingerPrint, fingerName, cId) {
    cId= this.customerId;
    console.log('save biometric')
    let screen = 'kyc-verification';
    this.fingerPrintService.saveBiometric(capFingerPrint, fingerName, cId, screen)
      .subscribe(data => {
        console.log("after save ", data);
        if(data){
          this.isScanned=true;
          this.ls.setItem('imageURLFace',data.fileUrl)
          // this.cdr.markForCheck();
          this.snack.open('Captured ' + fingerName + ' Finger', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
        }
        else{
          this.isFaliure = true;
        }
        
      },err => this.isFaliure = true);
  }
  done(){
    this.closeClick(this.isScanned) ;
    }

}
