import { Component, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';

@Component({
  selector: 'app-sign-later-loan',
  templateUrl: './sign-later-loan.component.html',
  styleUrls: ['./sign-later-loan.component.scss']
})
export class SignLaterLoanComponent implements OnInit {
  accountId: any;
  isDone: boolean = false;  
  cifNumber:number;
  email:any;
  constructor(public dialogRef: MatDialogRef<SignLaterLoanComponent>,private ls: LocalStoreService,
    private appApiService: ApplicationEntryStageService) { }

  ngOnInit(): void {
    this.accountId = this.ls.getItem('loanAccountId');
    this.fetchingEmailData();
  }
  Yes(): void {
    this.dialogRef.close("Yes");
  }
  close1()
  {    
      this.dialogRef.close(true);     

  } 
  fetchingEmailData(){
    this.cifNumber=this.ls.getItem('cId');
    this.appApiService.fetchByCif(this.cifNumber).subscribe((res: any) => {
      console.log(res);
      console.log(res.customerInfoList[0].primaryEmailAdress);
      this.email = res.customerInfoList[0].primaryEmailAdress;

    }, (err: any) => {
      console.error(err);
    })
  }
  }


