import { FingerPrintCaptureService } from './../../../shared/services/finger-print-capture.service';
import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ApiService, API_URL } from 'app/shared/services/api.service';
import { MatSnackBar } from "@angular/material/snack-bar";
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-kyc-iris-scan',
  templateUrl: './kyc-iris-scan.component.html',
  styleUrls: ['./kyc-iris-scan.component.scss']
})
export class KycIrisScanComponent implements OnInit {
  customerId: string;
  msg: string;
  isScanned:boolean=false;
  refreshedCustId: any;
  isFailure: boolean = false;
  resData: any;

  constructor(public dialogRef: MatDialogRef<KycIrisScanComponent>,
    private snack: MatSnackBar,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private fingerPrintService: FingerPrintCaptureService,
    private ls: LocalStoreService) { }

  ngOnInit(): void {
    this.customerId = sessionStorage.getItem('customer_id');
    if(this.data.customerId)
    this.customerId =  this.data.customerId;
    this.refreshedCustId=this.data.customerId;
    console.log('customer id :: ', this.customerId ,  this.refreshedCustId);
  }
  closeClick(isScanned) {
    console.log("close click")
    // [routerLink]="['/others/kyc']" 
    this.dialogRef.close({
      message: 'Confirm',
      isScanned:isScanned,
      data:this.resData
    });
    // this.router.navigateByUrl('/others/kyc'); 
  }

  scan() {
    const obj = {
      fileUrl: `${API_URL}/rest/upload/downloadDocs/?fileName=iris_biometric.jpg`
    };
    this.saveBiometric(obj, 'IRIS', this.refreshedCustId);
  }

  saveBiometric(capFingerPrint, fingerName, cId) {
    cId = this.customerId;
    console.log('save biometric')
    let screen = 'kyc-verification';
    this.fingerPrintService.saveBiometric(capFingerPrint, fingerName, cId, screen)
      .subscribe(data => {
        if(data){
          this.resData = data
          console.log("after save ", data);
          this.isScanned = true;
          this.isFailure=false;
          // this.cdr.markForCheck();
          this.snack.open('Captured ' + fingerName + ' Finger', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
        }
        else{
          this.isFailure = true;
          this.isScanned = false;
        }
        
      }, err => {
        this.isFailure = true;
        this.isScanned = false;
      }
      );
  }
  done(){
  this.closeClick(this.isScanned) ;
  }

}
