import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { SignaturePad } from 'angular2-signaturepad';

@Component({
  selector: 'app-kyc-esign',
  templateUrl: './kyc-esign.component.html',
  styleUrls: ['./kyc-esign.component.scss']
})
export class KycEsignComponent implements OnInit {
  @ViewChild(SignaturePad) signaturePad: SignaturePad;
  signOptions = true;
  onSelection = false;
  eSignnow = false;
  eSignlater = false;


  public signaturePadOptions: Object = { // passed through to szimek/signature_pad constructor

  };
  constructor(public dialogRef: MatDialogRef<KycEsignComponent>) { }
  ngOnInit(): void {
  }
  ngAfterViewInit() {
    this.signaturePad.set('minWidth', 5);
    this.signaturePad.clear();
    console.log('test console');
  }

  drawComplete() {
    console.log(this.signaturePad.toDataURL());
  }

  drawStart() {
    console.log('begin drawing');
  }
  closeClick() {
    console.log("close click")
    // [routerLink]="['/others/kyc']" 
    this.dialogRef.close({
      message: 'Confirm',
      action: 'closed'
    });
    // this.router.navigateByUrl('/others/kyc'); 
  }
  signNow() {
    console.log("Sign Now");
    this.onSelection = true;
    this.eSignnow = true;
    this.signOptions = false;
  }
  signLater() {
    console.log("Sign Later");
    this.onSelection = true;
    this.eSignlater = true;
    this.signOptions = false;
  }
}
