import { Component, Input, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-card-initiation-summary',
  templateUrl: './card-initiation-summary.component.html',
  styleUrls: ['./card-initiation-summary.component.scss']
})
export class CardInitiationSummaryComponent implements OnInit {
  existingCustomer:any;
  cardType:any;
  customerAccount:any;
  applicantName:any;
  currency:any;
  employmentType:any;
  affinityProgram:any;
  cardId: any;
  cardInitaionDetails: any;
  @Input() public cardInitiationInfo: any;
  appEntrySummaryView: boolean = false;
  cardInitiationDetails: any;
  resp: any;
  kycReference:any;
  kycStatus:any;
  customerId:any;
  nameOnCard:string;
  isExistingCustomer:boolean=false;

  constructor(public dialogRef: MatDialogRef<CardInitiationSummaryComponent>,private ls: LocalStoreService,
    private apiService:ApiService,private dataService: DataService,private router: Router) { }

  ngOnInit(): void {
    this.cardId=this.ls.getItem('cardId');
    this.getCardInitiationDetails();
    this.loadData(this.cardInitiationInfo);
  }
  goToClose(): void {
    this.dialogRef.close();
  }

  loadData(cardInitiationInfo){
    if (cardInitiationInfo == null) {
      this.appEntrySummaryView = false;
      this.getCardInitiationDetails();
    } else {
      if (cardInitiationInfo.isAppEntryView) {
        this.appEntrySummaryView = true;
        this.cardInitaionDetails = cardInitiationInfo.data;
        console.log('cardInitiationInfo',this.cardInitaionDetails)
        this.existingCustomer=this.cardInitaionDetails.existingCustomer;
      }
    }
  }
  getCardInitiationDetails(){
    this.apiService.getCardInitiationDetails(this.cardId)
    .subscribe((resp: any) => {
      this.isExistingCustomer=resp.existingCustomer;

      console.log("data",resp);
      this.cardInitaionDetails=resp;
     
    });
  }
  goToEdit() {
    this.dialogRef.close();
   // this.dataService.sendNavParam(this.assetValued);
   this.dataService.sendNavParam(this.cardInitaionDetails);
    this.router.navigate([`/cardaccount/applicationEntryStage/list/cardInitiation`]);
  } 

}
