import { Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ApiService, API_URL } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { CutomerFingerPrintComponent } from 'app/views/others/cutomer-finger-print/cutomer-finger-print.component';
import { KycEsignComponent } from 'app/views/others/kyc-esign/kyc-esign.component';
import { KycFaceScanComponent } from 'app/views/others/kyc-face-scan/kyc-face-scan.component';
import { KycIrisScanComponent } from 'app/views/others/kyc-iris-scan/kyc-iris-scan.component';

@Component({
  selector: 'app-kyc-biometric-update',
  templateUrl: './kyc-biometric-update.component.html',
  styleUrls: ['./kyc-biometric-update.component.scss']
})
export class KycBiometricUpdateComponent implements OnInit {
biometricUpdateForm:FormGroup;
  biometricList: any;
disableAddAndDelete: any;
  disableFinish: boolean;
  selectedOptions2: any[] = [];
  biometrics_Type: any;
  diaplayBioCard: boolean;
  imageURLBioScan: any;
  biometricScan: boolean;
  biometricData: any[] = [];
  customerId: any;
  imageURLIrish: string;
  irisScan: boolean;
  faceScan: boolean;
  imageURLFace: string;
  eScan: boolean;
  constructor(
    private dialogRef: MatDialogRef<KycBiometricUpdateComponent>,
    @Inject(MAT_DIALOG_DATA) private data,
    private formBuilder: FormBuilder,
    private apiService: ApiService,
    private ls: LocalStoreService,
    private dialog: MatDialog
  ) { }

  ngOnInit(): void {
    console.log(this.data);
    console.log(this.biometrics_Type);
    this.customerId = this.ls.getItem('customerId')
    this.apiService.getDropDownValues("biometrics_Type").subscribe(responseData => {
      this.biometrics_Type = responseData;
      console.log(this.biometrics_Type);
  
      console.log(responseData);
      
    });
    this.buildBiometricForm();
    for(let i=0; i<this.data.customerDocuments.length;i++){
if(this.data.custDocuments[i].documentName == 'FINGERPRINT' || this.data.custDocuments[i].documentName=='IRIS' || this.data.custDocuments[i].documentName == 'FACE')
this.addBioDocsData(this.data.custDocuments[i].id,this.data.custDocuments[i].documentName,this.data.custDocuments[i].fileUrl)
    // this.biometricUpdateForm.get('biometricList')[i].id.setValue(this.data.custDocuments[i].id)
    // this.biometricUpdateForm.get('biometricList')[i].documentName.setValue(this.data.custDocuments[i].fileUrl)
    // this.biometricUpdateForm.get('biometricList')[i].documentType.setValue(this.data.custDocuments[i].documentName)
    }
  }
  buildBiometricForm(){
    this.biometricUpdateForm = this.formBuilder.group({
       biometricList: this.formBuilder.array([]),
       id: [""],
     });
     this.biometricUpdateForm.valueChanges.subscribe(data => {
      if (data.biometricList.length > 0) {
        this.disableFinish = true;
        this.selectedOptions2 = [];
        data.biometricList.forEach((element, index) => {
          this.selectedOptions2.push(element.bioDocumentType);
        });
      }
      if (data.biometricList.length == 3) {
        this.disableAddAndDelete = true;
      } else {
        this.disableAddAndDelete = false;
      }
    })
  }
  createItem() {
    return this.formBuilder.group({
      id: [""],
      documentName: ["", Validators.required],
      documentType: ["", Validators.required],

    });
  }
  addBioDocsData(data, type, fileUrl) {
    if (data != '') {
      this.biometricList = this.biometricUpdateForm.get('biometricList') as FormArray;
      this.biometricList.push(this.createBioItemData(data, type, fileUrl));
    }
  }
  createBioItemData(data: any, type: any, fileUrl): any {
    return this.formBuilder.group({
      id: data,
      bioDocumentType: type,
      fileUrl: fileUrl
    });
  }
  addBioDocs() {
    this.biometricList = this.biometricUpdateForm.get('biometricList') as FormArray;
    this.biometricList.push(this.createBioItem());
  }
  createBioItem() {
    return this.formBuilder.group({
      id: [""],
      bioDocumentType: ["", Validators.required],
      fileUrl: [""]
    });

  }
  getBiometricType(){
  this.apiService.getDropDownValues("biometrics_Type").subscribe(responseData => {
    this.biometrics_Type = responseData;
  });
}
isDocumentOptionDisabled2 = (value) => {
  return this.selectedOptions2.includes(value);
}
openDialogForType(data) {
  if (data.bioDocumentType == "Fingerprint Scan") {
    const dialogRef = this.dialog.open(CutomerFingerPrintComponent, {
      data: { bioData: this.biometricData,
      customerId:this.customerId },
      disableClose: true,
    });
    dialogRef.afterClosed().subscribe((submit) => {
      this.biometricData = submit.data;
      if (this.biometricData.length > 0) {
        this.diaplayBioCard = true;
        this.biometricScan = false;
      } else {
        if (submit.isScanned) {
          this.biometricScan = true;
          this.imageURLBioScan = this.ls.getItem('imageURLBio');
          this.diaplayBioCard = false;
        }
      }
      this.disableFinish = false;
    });
  }
  if (data.bioDocumentType == "Irish Scan") {
    // IrisScan2Component
    const dialogRef = this.dialog.open(KycIrisScanComponent, {
      data: { number: "9656849771", type: "Mobile Number",customerId:this.customerId  },
      disableClose: true,
    });
    dialogRef.afterClosed().subscribe((submit) => {
      if (submit.isScanned) {
        this.irisScan = true;
        this.imageURLIrish = `${API_URL}/rest/upload/downloadDocs/?fileName=iris_biometric.jpg`;

      } else {
        this.irisScan = false;
        this.imageURLIrish = '';
      }
      this.disableFinish = false;
    });
  }
  if (data.bioDocumentType == "Face Scan") {

    const dialogRef = this.dialog.open(KycFaceScanComponent, {
      data: { number: "9656849771", type: "Mobile Number" ,customerId:this.customerId  },
      disableClose: true,
    });
    dialogRef.afterClosed().subscribe((submit) => {
      console.log('submit :: ', submit);
      if (submit.isScanned) {
        this.faceScan = true;
        this.imageURLFace = `${API_URL}/rest/upload/downloadDocs/?fileName=face_biometric.jpg`;
      } else {
        this.imageURLFace = '';
        this.faceScan = false;
      }
      this.disableFinish = false;
    });
  }
  if (data.bioDocumentType == "E-Sign") {
    const dialogRef = this.dialog.open(KycEsignComponent, {
      data: { number: "9656849771", type: "Mobile Number" },
      disableClose: true,
    });
    dialogRef.afterClosed().subscribe((submit) => {
      console.log('submit :: ', submit);
      if (submit.action == 'closed') {
        this.eScan = true;
      } else {
        this.eScan = false;
      }
    });
  }
}
deleteBioMetric(report, index) {
  const deleteRecord = this.biometricUpdateForm.get('biometricList') as FormArray;
  deleteRecord.removeAt(index);
  this.imageURLIrish = '';
  this.imageURLFace = '';
  this.imageURLBioScan = '';
  this.apiService.deleteCustomerDoc(this.customerId, report.id).subscribe((data) => {
  });
}
onSubmit(){
  this.dialogRef.close({
    status:'INITIATED',
    screen:'biometric'
   })
}
  close() {
    this.dialogRef.close({
      status: 'UNDER_PROCESS'
    })
  }
}
