import { Component, OnInit } from '@angular/core';
import { NULL_EXPR } from '@angular/compiler/src/output/output_ast';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { QualitativeScoreboardSummary } from 'app/shared/models/qualitativeScoreboardSummary';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-cardaccount-qualitative-summary',
  templateUrl: './cardaccount-qualitative-summary.component.html',
  styleUrls: ['./cardaccount-qualitative-summary.component.scss']
})
export class CardaccountQualitativeSummaryComponent implements OnInit {
  accountDetails: object;
  loanaccountID: any;
  loanAccountId:any;
  constructor(
    public dialogRef: MatDialogRef<CardaccountQualitativeSummaryComponent>,
    private router:Router, 
    private ls: LocalStoreService,
    private apiService: ApiService,
    private dataService: DataService
  ) { }
  title: string = "Card Account Qualitative Scoreboard";
  imgUrl = "assets/images/flagUSA.jpg";
  accountId:any;
  cardId:any;
  qualitativeScoreboardSummary : any;
  ngOnInit(): void {
    this.cardId = this.ls.getItem('cardId');
    console.log("CARD ID:: ",this.cardId);
    this.getAllData(this.cardId);
  }

  getAllData(id){
    // id == '' || id == null ? id = 1234 : id;
    this.apiService.getCardQualitativeScorecardDetails(this.cardId).subscribe((response) => {
      this.qualitativeScoreboardSummary = response;
      console.log(this.qualitativeScoreboardSummary);
       console.log(this.qualitativeScoreboardSummary.scoreCardMapping);
    });
  }

  Close(): void {
    this.dialogRef.close();
  }

  goAndEdit(){
    console.log(this.qualitativeScoreboardSummary);
    this.dialogRef.close();
    this.dataService.sendNavParam(this.qualitativeScoreboardSummary);
    this.router.navigate(['/cardaccount/underwritingStage/qualitative']);
   
  }

}



