import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-offer-accept-reject-summary',
  templateUrl: './offer-accept-reject-summary.component.html',
  styleUrls: ['./offer-accept-reject-summary.component.scss']
})
export class OfferAcceptRejectSummaryComponent implements OnInit {
  offerAcceptRejectvalue: any;
  loanaccountID: any;
  loanInfo: any;

  constructor(public dialogRef: MatDialogRef<OfferAcceptRejectSummaryComponent>, private router: Router,
    private api: ApiService,private ls:LocalStoreService) { }

  ngOnInit(): void {
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.getOfferAcceptReject();
    }, 10);
   
  }
  goToClose(): void {
    this.dialogRef.close();
  }
  goToEdit() {
    this.dialogRef.close();
    this.router.navigate([`/loan/account/account-creation-stage/offer-accept-reject`]);
    

    // this.router.navigate([`/home/application/customer-information/${this.productAccountIdCust}`]);
  }
  getOfferAcceptReject() {
    this.api.getOfferAcceptReject('200')
     .subscribe((offerAcceptReject: any)=> {
      console.log(offerAcceptReject);
      this.offerAcceptRejectvalue=offerAcceptReject;
      
    });
  }

}
